
































import { Vue, Component, Prop } from 'vue-property-decorator'
import CustomizerFieldGroupDropdownButton from './CustomizerFieldGroupDropdownButton.vue'
import Diagram, { Field, FieldGroup } from '@/diagram'

@Component({
  components: {
    CustomizerFieldGroupDropdownButton,
  },
})

export default class CustomizerFieldGroup extends Vue {
  @Prop({ type: Diagram, required: true })
  readonly diagram!: Diagram

  @Prop({ type: Object, required: true })
  readonly fieldGroup!: FieldGroup

  get activeFields (): Field[] {
    return this.fieldGroup.fields.filter(field => field.active)
  }
}
