






















































































import { Vue, Component, Model, Ref, Inject, Prop, Watch } from 'vue-property-decorator'
import Diagram, { Entity, EntityDefinition, EntityType, SearchResult } from '@/diagram'
import TextField from './TextField.vue'
import BaseButton from './BaseButton.vue'
import { watchViewport } from '@/util/watch-viewport'
import { watchRect } from '@/util/watch-rect'
import DiagramVue from './Diagram.vue'

export interface Suggestion extends SearchResult {
  id: string
  entity?: Entity
  entityDefinition?: EntityDefinition
}

type Promisable<T> = T | Promise<T>

type Cache = Map<string, Promise<Suggestion[]>>

const caches = new Map<Diagram, Cache>()

const timeout = (ms: number) => new Promise<void>(resolve => setTimeout(() => resolve(), ms))

function createDeferred<T> () {
  let resolve: any = null
  let reject: any = null

  const promise = new Promise<T>((_resolve, _reject) => {
    resolve = _resolve
    reject = _reject
  })

  if (!resolve || !reject) throw Error()

  return {
    promise,
    resolve: resolve as (value: T | PromiseLike<T>) => void,
    reject: reject as (reason?: any) => void,
  }
}

@Component({
  components: {
    TextField,
    BaseButton,
  },
})

export default class EntitySearchBar extends Vue {
  @Inject()
  readonly diagramVm!: DiagramVue

  @Prop(EntityType)
  readonly entityType?: EntityType

  @Model('change', Object)
  readonly selectedSuggestion?: Suggestion

  @Ref()
  readonly textField!: TextField

  @Ref()
  readonly suggestionsEl!: HTMLElement

  focused = false
  inputFocused = false
  suggestionsFocused = false

  showSuggestions = false

  inputText = ''

  suggestionsPromise: Promise<void> | null = null
  suggestions: Suggestion[] | null = null
  activeSuggestionIndex = 0

  viewportRect: DOMRect | null = null
  textFieldRect: DOMRect | null = null

  get diagram () {
    return this.diagramVm.diagram
  }

  get cache () {
    let cache = caches.get(this.diagram)
    if (!cache) {
      cache = new Map
      caches.set(this.diagram, cache)
    }
    return cache
  }

  get suggestionsStyle () {
    if (!this.viewportRect || !this.textFieldRect) return {}

    return {
      top: `${this.textFieldRect.bottom}px`,
      left: `${this.textFieldRect.left}px`,
      width: `${this.textFieldRect.width}px`,
      maxHeight: `${this.viewportRect.bottom - this.textFieldRect.bottom}px`,
    }
  }

  get loadingSuggestions () {
    return !!this.suggestionsPromise
  }

  get noSuggestions () {
    return !this.suggestions?.length
  }

  get activeSuggestion () {
    return this.suggestions?.[this.activeSuggestionIndex] ?? null
  }

  onDestroy?: () => void

  mounted () {
    const { unwatch: unwatchViewport } = watchViewport(rect => this.viewportRect = rect)
    const { unwatch: unwatchTextField } = watchRect(this.textField.$el as HTMLElement, rect => this.textFieldRect = rect)

    this.$watch(() => [ this.inputFocused, this.suggestionsFocused ], ([ inputFocused, suggestionsFocused ]) => {
      requestAnimationFrame(() => {
        // Focus changed since last frame, return out
        if (this.inputFocused !== inputFocused || this.suggestionsFocused !== suggestionsFocused) return

        this.focused = inputFocused || suggestionsFocused
      })
    })

    this.onDestroy = () => {
      unwatchViewport()
      unwatchTextField()
    }
  }

  beforeDestroy () {
    this.onDestroy?.()
  }

  onInputKeyDown (e: KeyboardEvent) {
    if (!this.showSuggestions) return

    const key = e.key || e.code

    switch (key) {
      case 'ArrowUp' :
      case 'Up' :
      case 'ArrowDown' :
      case 'Down' : {
        e.preventDefault()

        const min = 0
        const max = Math.max((this.suggestions?.length ?? 0) - 1, 0)

        let newIndex = this.activeSuggestionIndex
        if (key.endsWith('Up')) newIndex--
        else newIndex++

        if (newIndex < min) newIndex = max
        else if (newIndex > max) newIndex = min

        this.activeSuggestionIndex = newIndex
        this.scrollActiveSuggestionIntoView()
        break
      }

      case 'Tab' :
      case 'Enter' : {
        if (!this.activeSuggestion) return

        // Ignore Shift + Tab
        if (e.shiftKey && e.code === 'Tab') break

        e.preventDefault()
        this.selectSuggestion(this.activeSuggestion)
        break
      }
    }
  }

  getSuggestionsFromDiagram (): Suggestion[] {
    const { entityType, inputText } = this

    if (!entityType || !inputText) return []

    return this.diagram.search(entityType, inputText).slice(0, 10).map(entity => {
      const { text, supertext, subtext } = entityType.searchResultBuilder({ id: entity.id, data: entity.data })

      return {
        id: entity.id,
        entity,
        text,
        supertext,
        subtext,
      }
    })
  }

  async fetchSuggestionsFromServer (): Promise<Suggestion[]> {
    const { entityType, inputText } = this
    const { searchEntities } = this.diagram.methods

    if (!entityType || !inputText || !searchEntities) return []

    const cacheKey = `${entityType.id};${inputText}`

    const cached = this.cache.get(cacheKey)
    if (cached) return cached

    const { promise, resolve } = createDeferred<Suggestion[]>()
    this.cache.set(cacheKey, promise)

    const entities = await searchEntities({ query: inputText, type: entityType.id })

    const suggestions = entities.map((entity): Suggestion => {
      const { text, supertext, subtext } = entityType.searchResultBuilder({ id: entity.id, data: entity.data ?? {} })
      return {
        id: entity.id,
        entityDefinition: entity,
        text,
        supertext,
        subtext,
      }
    })

    resolve(suggestions)
    return suggestions
  }

  async loadSuggestions (debounce = true) {
    const { entityType, inputText } = this

    if (!entityType || !inputText) {
      this.suggestionsPromise = null
      this.suggestions = null
      return
    }

    const { promise, resolve } = createDeferred<void>()
    this.suggestionsPromise = promise

    if (debounce) {
      await timeout(250)
      if (this.suggestionsPromise !== promise) return await this.suggestionsPromise
    }

    const fetchedSuggestions = await this.fetchSuggestionsFromServer()

    if (this.suggestionsPromise !== promise) return

    this.suggestionsPromise = null
    this.suggestions = [
      ...this.getSuggestionsFromDiagram(),

      // Hide suggestions that are already in the diagram
      ...fetchedSuggestions.filter(suggestion => !this.diagram.activeDiagram.data.entitiesMap.has(suggestion.id)),
    ]
    resolve()
  }

  scrollActiveSuggestionIntoView () {
    if (!this.suggestions?.length) return

    const el = this.suggestionsEl.querySelectorAll('.lassox-diagram__EntitySearchBar_suggestion')[this.activeSuggestionIndex]
    if (!el) return

    const rect = el.getBoundingClientRect()
    const parentRect = this.suggestionsEl.getBoundingClientRect()

    if (rect.top < parentRect.top) {
      this.suggestionsEl.scrollTop -= parentRect.top - rect.top
    } else if (rect.bottom > parentRect.bottom) {
      this.suggestionsEl.scrollTop -= parentRect.bottom - rect.bottom
    }
  }

  selectSuggestion (suggestion: Suggestion | null) {
    if (suggestion !== this.selectedSuggestion) this.$emit('change', suggestion)
    this.inputText = suggestion?.text ?? suggestion?.id ?? ''
    this.$nextTick(() => {
      this.showSuggestions = false
    })
  }

  @Watch('entityType')
  onEntityTypeChanged () {
    this.suggestionsPromise = null
    this.suggestions = null
    this.loadSuggestions()
  }

  @Watch('selectedSuggestion', { immediate: true, })
  onSelectedSuggestionChanged () {
    this.selectSuggestion(this.selectedSuggestion ?? null)
  }

  @Watch('focused')
  onFocusedChanged () {
    this.showSuggestions = this.focused && !!this.inputText

    if (!this.focused) {
      if (!this.inputText || !this.selectedSuggestion) {
        this.selectSuggestion(null)
      } else {
        this.selectSuggestion(this.selectedSuggestion)
      }
    }
  }

  @Watch('inputText', { immediate: true })
  onInputTextChanged () {
    const { focused, inputText } = this

    this.activeSuggestionIndex = 0

    if (inputText) {
      this.loadSuggestions()
      this.showSuggestions = focused
    } else {
      this.selectSuggestion(null)
      this.suggestionsPromise = null
      this.suggestions = null
    }
  }

  @Watch('suggestions')
  onSuggestionsChanged () {
    this.activeSuggestionIndex = 0
  }
}
