























import { Vue, Component, Prop, Watch, Ref, Provide } from 'vue-property-decorator'
import { watchRect, unwatchRect } from '@/util/watch-rect'

@Component({})

export default class DropdownMenu extends Vue {
  @Provide()
  get dropdownMenuVm () { return this }

  @Prop({ type: HTMLElement })
  readonly buttonEl?: HTMLElement

  @Prop({ type: Function })
  readonly close?: () => void

  @Ref()
  readonly boundsEl!: HTMLElement

  @Ref()
  readonly dropdownMenuEl!: HTMLElement

  buttonRect: DOMRect | null = null
  boundsRect: DOMRect | null = null
  dropdownMenuRect: DOMRect | null = null

  get dropdownMenuStyle () {
    const { buttonRect, boundsRect, dropdownMenuRect } = this

    if (!buttonRect || !boundsRect || !dropdownMenuRect) return null

    const spaceAbove = buttonRect.top - boundsRect.top
    const spaceBelow = boundsRect.bottom - buttonRect.bottom
    const spaceLeft = buttonRect.right - boundsRect.left
    const spaceRight = boundsRect.right - buttonRect.left

    let hAlign: 'left' | 'right' = 'left'
    let vAlign: 'above' | 'below' = 'below'

    const minWidth = Math.max(buttonRect.width, 112)
    const maxWidth = Math.min(Math.max(spaceLeft, spaceRight), 280)
    let maxHeight: number
    const transforms = []

    if (maxWidth > spaceRight && spaceLeft > spaceRight) {
      hAlign = 'right'
    }

    if (hAlign == 'left') {
      transforms.push(`translateX(${buttonRect.left}px)`)
    } else {
      transforms.push(`translateX(${buttonRect.right}px) translateX(-100%)`)
    }

    if (spaceBelow <= 280 && spaceAbove > spaceBelow) {
      vAlign = 'above'
    }

    if (vAlign == 'above') {
      maxHeight = spaceAbove
      transforms.push(`translateY(${buttonRect.top}px) translateY(-100%)`)
    } else {
      maxHeight = spaceBelow
      transforms.push(`translateY(${buttonRect.bottom}px)`)
    }

    return {
      minWidth: `${minWidth}px`,
      maxWidth: `${maxWidth}px`,
      maxHeight: `${maxHeight}px`,
      transform: transforms.join(' '),
    }
  }

  mounted () {
    this.onButtonElChanged(this.buttonEl)
  }

  updateButtonRect (rect: DOMRect) {
    this.buttonRect = rect
  }

  updateBoundsRect (rect: DOMRect) {
    this.boundsRect = rect
  }

  updateDropdownMenuRect (rect: DOMRect) {
    this.dropdownMenuRect = rect
  }

  onFocusOut (event: FocusEvent) {
    const newFocusEl = event.relatedTarget

    if (newFocusEl instanceof HTMLElement) {
      if (this.buttonEl?.contains(newFocusEl)) return
      if (this.dropdownMenuEl.contains(newFocusEl)) return
    }

    this.close?.()
  }

  @Watch('buttonEl')
  onButtonElChanged (buttonEl?: HTMLElement | null, oldButtonEl?: HTMLElement | null) {
    if (oldButtonEl) {
      unwatchRect(oldButtonEl, this.updateButtonRect)
      oldButtonEl.removeEventListener('focusout', this.onFocusOut)
    }

    if (!buttonEl) {
      unwatchRect(this.boundsEl, this.updateBoundsRect)
      unwatchRect(this.dropdownMenuEl, this.updateDropdownMenuRect)
      this.dropdownMenuEl.removeEventListener('focusout', this.onFocusOut)
    } else {
      if (!oldButtonEl) {
        watchRect(this.boundsEl, this.updateBoundsRect)
        watchRect(this.dropdownMenuEl, this.updateDropdownMenuRect)
        this.dropdownMenuEl.addEventListener('focusout', this.onFocusOut)
      }

      watchRect(buttonEl, this.updateButtonRect)
      buttonEl.addEventListener('focusout', this.onFocusOut)
    }
  }
}
