import Vue from 'vue'
import DiagramVue from './components/Diagram.vue'
import DiagramEventBus from './types/DiagramEventBus'
import DiagramData, { DiagramDataDefinition } from './types/DiagramData'
import { getDeep, setDeep } from './util/get-deep-property'
import SavedDiagram, { createSavedDiagram } from './types/SavedDiagram'
import cloneDeep from 'lodash.clonedeep'
import { isRecord } from './util/type-guards'

export default class Diagram {
  static init (config: DiagramConfig) {
    return new Diagram(config)
  }

  element?: Element
  vm?: Vue

  eventBus = new DiagramEventBus()

  mainEntityId: string

  entityTypes: EntityTypes
  relationTypes: RelationTypes

  fieldGroups: FieldGroup[]
  fields: Field[]

  filters: Filter[]

  layouts: LayoutDefinition[]

  methods: Pick<DiagramConfigMethods, (
    | 'saveDiagram'

    | 'getPrintTitle'
    | 'getPrintFilename'
    | 'convertPngToPdf'

    | 'getEntityData'

    | 'searchEntities'
  )>

  enableEditing = false
  enableStyleEditing = false
  enableDragAndDrop = false
  enableExpand = false
  enableSaving = false
  enableSharing = false
  saveSettingsToLocalStorage: boolean

  activeDiagram: ActiveDiagram

  hasUnsavedChanges = false
  isSaving = false

  constructor (config: DiagramConfig) {
    this.mainEntityId = config.mainEntityId

    this.entityTypes = new EntityTypes(config.entityTypes ?? [])
    this.relationTypes = new RelationTypes(config.relationTypes ?? [])

    this.fieldGroups = config.fieldGroups?.map(f => new FieldGroup(this, f)) ?? []
    this.fields = this.fieldGroups.flatMap(fieldGroup => fieldGroup.fields)

    this.filters = config.filters?.map(f => new Filter(this, f)) ?? []

    this.layouts = config.layouts ?? []

    this.methods = {
      saveDiagram: config.methods?.saveDiagram,

      getPrintTitle: config.methods?.getPrintTitle,
      getPrintFilename: config.methods?.getPrintFilename,
      convertPngToPdf: config.methods?.convertPngToPdf,

      getEntityData: config.methods?.getEntityData,

      searchEntities: config.methods?.searchEntities,
    }

    if (config.enableEditing) {
      this.enableEditing = true
    }

    if (config.enableStyleEditing) {
      this.enableStyleEditing = true
    }

    if (config.enableDragAndDrop) {
      this.enableDragAndDrop = true
    }

    if (config.enableExpand) {
      this.enableExpand = true
    }

    if (config.enableSaving) {
      this.enableSaving = true

      if (!config.enableEditing) {
        console.warn('Cannot enable saving without enabling editing.')
        this.enableSaving = false
      }

      if (!config.methods?.saveDiagram) {
        console.warn('Cannot enable saving without providing a saveDiagram method.')
        this.enableSaving = false
      }
    }

    if (config.enableSharing) {
      this.enableSharing = true

      if (!config.enableSaving) {
        console.warn('Cannot enable sharing without enabling saving.')
        this.enableSharing = false
      }
    }

    this.saveSettingsToLocalStorage = config.saveSettingsToLocalStorage ?? false

    for (const entityType of this.entityTypes.items) {
      entityType.fieldGroups = this.fieldGroups.map(fg => new FieldGroup(this, {
        ...fg,
        fields: fg.fields.filter(f => f.entityTypes.has(entityType)),
      }))

      entityType.fields = entityType.fieldGroups.flatMap(fg => fg.fields)
      entityType.searchableFields = entityType.fields.filter(field => field.searchable)
    }

    for (const relationType of this.relationTypes.items) {
      relationType.fieldGroups = this.fieldGroups.map(fg => new FieldGroup(this, {
        ...fg,
        fields: fg.fields.filter(f => f.relationTypes.has(relationType)),
      }))

      relationType.fields = relationType.fieldGroups.flatMap(fg => fg.fields)
      relationType.searchableFields = relationType.fields.filter(field => field.searchable)
    }

    this.activeDiagram = new ActiveDiagram(this)

    this.methods.getEntityData?.({
      ids: [this.mainEntityId],
      relationTypes: this.relationTypes.items.map(rt => rt.id),
      parents: 10,
      children: 10,
    })
      .then(data => {
        requestAnimationFrame(() => {
          this.setActiveDiagram(new ActiveDiagram(this, {
            data: new DiagramData(this, { data }),
          }))
        })
      })

    this.eventBus.on([ 'dataUpdated', 'settingsChanged' ], () => {
      this.hasUnsavedChanges = true
    })
  }

  render (container: Element) {
    if (this.element) this.element.parentElement?.removeChild(this.element)
    if (this.vm) this.vm.$destroy()

    const element = document.createElement('div')
    container.appendChild(element)

    this.element = element
    this.vm = new Vue({
      el: element,
      render: h => h(DiagramVue, {
        props: {
          diagram: Vue.observable(this),
        },
      }),
    })

    return element
  }

  setActiveDiagram (activeDiagram: ActiveDiagram) {
    this.activeDiagram = activeDiagram
    this.hasUnsavedChanges = false
    this.eventBus.emit({ name: 'settingsChanged' })
  }

  async save (overwrite = true) {
    const { saveDiagram } = this.methods

    if (this.isSaving || !saveDiagram) return

    this.isSaving = true
    const savedDiagram = createSavedDiagram(this)
    await saveDiagram({
      ...savedDiagram,
      id: overwrite ? savedDiagram.id : '',
    })
    this.hasUnsavedChanges = false
    this.isSaving = false
  }

  load (savedDiagram: SavedDiagram) {
    const activeDiagram = new ActiveDiagram(this, {
      id: savedDiagram.id,
      title: savedDiagram.title,
      description: savedDiagram.description,
      shared: savedDiagram.shared,

      data: new DiagramData(this, {
        data: savedDiagram.data,
        changes: savedDiagram.changes,
      })
    })

    if (savedDiagram.settings) {
      const { settings } = activeDiagram

      if (savedDiagram.settings.activeFields) {
        for (const id in settings.activeFields) {
          settings.activeFields[id] = false
        }

        for (const id of savedDiagram.settings.activeFields) {
          if (id in settings.activeFields) settings.activeFields[id] = true
        }
      }

      if (savedDiagram.settings.activeFilters) {
        for (const id in settings.activeFilters) {
          settings.activeFilters[id] = false
        }

        for (const id of savedDiagram.settings.activeFilters) {
          if (id in settings.activeFilters) settings.activeFilters[id] = true
        }
      }

      if (savedDiagram.settings.activeLayout) {
        const id = savedDiagram.settings.activeLayout
        const layout = this.layouts.find(l => l.id === id)
        if (layout) settings.activeLayout = layout
      }
    }

    this.setActiveDiagram(activeDiagram)
  }

  reset () {
    this.activeDiagram.reset()
    this.eventBus.emit({ name: 'settingsChanged' })
    this.eventBus.emit({ name: 'activeFieldsChanged' })
    this.eventBus.emit({ name: 'activeFiltersChanged' })
    this.eventBus.emit({ name: 'graph.fit' })
  }

  search (type: EntityType, searchString: string): Entity[]

  search (type: RelationType, searchString: string): Relation[]

  search (type: EntityType | RelationType, searchString: string): (Entity | Relation)[] {
    searchString = searchString.trim().toLowerCase()

    if (!searchString || !type.searchable || !type.searchableFields.length) return []

    const results: (Entity | Relation)[] = []

    for (const item of this.activeDiagram.data[type instanceof EntityType ? 'entities' : 'relations']) {
      if (item.type !== type) continue

      for (const field of type.searchableFields) {
        const value = field.getValue(item.data)
        const string = (`${(field.formatter ? field.formatter(value) : value) ?? ''}`).trim().toLowerCase()

        if (string && string.includes(searchString)) {
          results.push(item)
          break
        }
      }
    }

    return results
  }
}

export interface DiagramConfig {
  mainEntityId: string
  entityTypes?: EntityTypeDefinition[]
  relationTypes?: RelationTypeDefinition[]
  fieldGroups?: FieldGroupDefinition[]
  filters?: FilterDefinition[]
  layouts?: LayoutDefinition[]
  methods?: DiagramConfigMethods
  enableEditing?: boolean
  enableStyleEditing?: boolean
  enableDragAndDrop?: boolean
  enableExpand?: boolean
  enableSaving?: boolean
  enableSharing?: boolean
  saveSettingsToLocalStorage?: boolean
}

export interface DiagramConfigMethods {
  getSavedDiagrams?: () => Promise<SavedDiagram[]>
  saveDiagram?: (savedDiagram: SavedDiagram) => Promise<void>
  deleteSavedDiagram?: (savedDiagram: SavedDiagram) => Promise<void>

  getPrintTitle?: (context: PrintContext) => string
  getPrintFilename?: (context: PrintContext) => string

  convertPngToPdf?: (context: {
    blob: Blob,
    title: string,
    filename: string,
    size: 'A5' | 'A4' | 'A3',
    orientation: 'portrait' | 'landscape',
  }) => Promise<Blob>

  getEntityData?: (options: {
    ids: string[], // Entity ids
    relationTypes: string[], // RelationType ids
    parents: number,
    children: number,
  }) => Promise<DiagramDataDefinition>

  searchEntities?: (context: {
    query: string // The search query
    type: string // EntityType id
  }) => Promise<EntityDefinition[]>
}

export interface PrintContext {
  mainEntity: {
    id: string,
    data: Record<string, any>,
  },
  hasDataChanges: boolean,
  date: Date,
  formattedDate: string,
  formattedTime: string,
}

export class ActiveDiagram {
  constructor (diagram: Diagram, options?: {
    id?: string
    title?: string
    description?: string
    shared?: boolean

    data?: DiagramData
    settings?: Settings
  }) {
    this.diagram = diagram

    this.id = options?.id ?? ''
    this.title = options?.title ?? ''
    this.description = options?.description ?? ''
    this.shared = options?.shared ?? false

    this.data = options?.data ?? new DiagramData(diagram)
    this.settings = options?.settings ?? new Settings(diagram)
  }

  diagram: Diagram

  id: string
  title: string
  description: string
  shared: boolean

  data: DiagramData
  settings: Settings

  reset () {
    this.data.reset()
    this.settings.reset()
  }
}

export class EntityTypes {
  constructor (items: EntityTypeDefinition[]) {
    this.items = items.map(item => new EntityType(item))
    this.itemsMap = new Map(this.items.map(item => [item.id, item]))
  }

  items: EntityType[]
  itemsMap: Map<string, EntityType>

  has (idOritem: string | EntityType) {
    return typeof idOritem == 'string'
      ? this.itemsMap.has(idOritem)
      : this.itemsMap.has(idOritem.id)
  }

  get (id: string) {
    const item = this.itemsMap.get(id)
    if (!item) throw Error(`No entity type with id "${id}"`)
    return item
  }

  getMultiple (ids: string[]) {
    return ids.map(id => this.get(id))
  }

  pick (ids: string[]) {
    return new EntityTypes(this.getMultiple(ids))
  }
}

export class RelationTypes {
  constructor (items: RelationTypeDefinition[]) {
    this.items = items.map(item => new RelationType(item))
    this.itemsMap = new Map(this.items.map(item => [item.id, item]))
  }

  items: RelationType[]
  itemsMap: Map<string, RelationType>

  has (idOritem: string | RelationType) {
    return typeof idOritem == 'string'
      ? this.itemsMap.has(idOritem)
      : this.itemsMap.has(idOritem.id)
  }

  get (id: string) {
    const item = this.itemsMap.get(id)
    if (!item) throw Error(`No relation type with id "${id}"`)
    return item
  }

  getMultiple (ids: string[]) {
    return ids.map(id => this.get(id))
  }

  pick (ids: string[]) {
    return new RelationTypes(this.getMultiple(ids))
  }
}

export class EntityType {
  id: string
  icon?: string
  labels: EntityTypeDefinition['labels']
  style: EntityStyle
  printStyle?: EntityStyle
  searchable: boolean
  searchResultBuilder: SearchResultBuilder

  fieldGroups: FieldGroup[] = []
  fields: Field[] = []
  searchableFields: Field[] = []

  constructor (options: EntityTypeDefinition) {
    this.id = options.id
    this.icon = options.icon ?? undefined
    this.labels = options.labels
    this.style = {
      backgroundColor: options.style?.backgroundColor ?? '#ffffff',
      borderColor: options.style?.borderColor ?? '#e0e0e0',
      borderStyle: options.style?.borderStyle ?? 'solid',
      borderWidth: options.style?.borderWidth ?? 2,
      labelsBackgroundColor: options.style?.labelsBackgroundColor ?? '#ffffff',
    }
    this.printStyle = options.printStyle
    this.searchable = options.searchable ?? false
    this.searchResultBuilder = options.searchResultBuilder ?? (context => ({ text: context.id }))
  }
}

export interface EntityTypeDefinition {
  id: string
  icon?: string
  labels: {
    singular: string
    plural: string
    editorLabel?: (data: Record<string, any>) => string
    editorDescription?: (data: Record<string, any>) => string
  }
  style?: EntityStyle
  printStyle?: EntityStyle
  searchable?: boolean
  searchResultBuilder?: SearchResultBuilder
}

export class RelationType {
  id: string
  labels: RelationTypeDefinition['labels']
  style: RelationStyle
  printStyle?: RelationStyle
  supports: RelationTypeSupport[]
  searchable: boolean

  fieldGroups: FieldGroup[] = []
  fields: Field[] = []
  searchableFields: Field[] = []

  constructor (options: RelationTypeDefinition) {
    this.id = options.id
    this.labels = options.labels
    this.style = {
      arrowFrom: options.style?.arrowFrom ?? false,
      arrowTo: options.style?.arrowTo ?? true,
      arrowSize: options.style?.arrowSize ?? 6,
      lineWidth: options.style?.lineWidth ?? 4,
      lineStyle: options.style?.lineStyle ?? 'solid',
      lineColor: options.style?.lineColor ?? '#d8d8d8',
    }
    this.printStyle = options.printStyle

    this.supports = options.supports.map(r => new RelationTypeSupport(r))
    this.searchable = options.searchable ?? false
  }
}

export type RelationTypeDefinition = {
  id: string
  labels: {
    singular: string
    plural: string
    singularFrom?: string
    pluralFrom?: string
    singularTo?: string
    pluralTo?: string
    editorLabel?: (data: Record<string, any>) => string
    editorDescription?: (data: Record<string, any>) => string
    newParent?: string
    newChild?: string
    existingParents?: string
    existingChildren?: string
  }
  style?: RelationStyle
  printStyle?: RelationStyle
  supports: RelationTypeSupportDefinition[]
  searchable?: boolean
}

export class RelationTypeSupport {
  from: string
  to: string
  allowAddExisting: boolean

  constructor (options: RelationTypeSupportDefinition) {
    this.from = options.from
    this.to = options.to
    this.allowAddExisting = options.allowAddExisting ?? false
  }
}

export interface RelationTypeSupportDefinition {
  from: string
  to: string
  allowAddExisting?: boolean
}

export type SearchResultBuilder = (context: SearchResultBuilderContext) => SearchResult

export interface SearchResultBuilderContext {
  id: string
  data: Record<string, any>
}

export interface SearchResult {
  text: string
  supertext?: string | null
  subtext?: string | null
}

export class Entity {
  diagram: Diagram

  readonly isEntity = true
  readonly isRelation = false

  type: EntityType
  id: string

  initialState: Pick<Entity, 'position' | 'data' | 'style'>

  position?: { x: number, y: number }
  data: Record<string, any>
  style: EntityStyle

  constructor (diagram: Diagram, options: EntityDefinition) {
    this.diagram = diagram

    this.type = diagram.entityTypes.get(options.type)
    this.id = options.id

    this.initialState = {
      position: cloneDeep(options.position),
      data: cloneDeep(options.data) ?? {},
      style: cloneDeep(options.style) ?? {},
    }

    this.position = cloneDeep(this.initialState.position)
    this.data = cloneDeep(this.initialState.data)
    this.style = cloneDeep(this.initialState.style)
  }

  get isMainEntity () {
    return this.id == this.diagram.mainEntityId
  }

  get fieldGroups () {
    return this.type.fieldGroups
  }
}

export interface EntityDefinition {
  type: string
  id: string
  position?: {
    x: number
    y: number
  }
  data?: Record<string, any>
  style?: EntityStyle
}

export interface EntityStyle {
  backgroundColor?: string
  borderColor?: string
  borderStyle?: 'solid' | 'dotted' | 'dashed'
  borderWidth?: number
  labelsBackgroundColor?: string
}

export class Relation {
  diagram: Diagram

  readonly isEntity = false
  readonly isRelation = true

  type: RelationType
  id: string
  from: string
  to: string

  initialState: Pick<Relation, 'data' | 'style'>

  data: Record<string, any>
  style: RelationStyle

  constructor (diagram: Diagram, options: RelationDefinition) {
    this.diagram = diagram

    this.type = diagram.relationTypes.get(options.type)
    this.id = options.id
    this.from = options.from
    this.to = options.to

    this.initialState = {
      data: cloneDeep(options.data) ?? {},
      style: cloneDeep(options.style) ?? {},
    }

    this.data = cloneDeep(this.initialState.data)
    this.style = cloneDeep(this.initialState.style)
  }

  get fieldGroups () {
    return this.type.fieldGroups
  }
}

export interface RelationDefinition {
  type: string
  id: string
  from: string
  to: string
  data?: Record<string, any>
  style?: RelationStyle
}

export interface RelationStyle {
  arrowFrom?: boolean
  arrowTo?: boolean
  arrowSize?: number
  lineWidth?: number
  lineStyle?: 'solid' | 'dotted' | 'dashed'
  lineColor?: string
}

export class FieldGroup {
  id: string
  title: string
  fields: Field[]

  constructor (diagram: Diagram, options: {
    id: string
    title: string
    fields: (Field | FieldDefinition)[]
  }) {
    this.id = options.id
    this.title = options.title
    this.fields = options.fields.map(f => f instanceof Field ? f : new Field(diagram, this, f))
  }
}

export interface FieldGroupDefinition {
  id: string
  title: string
  fields: FieldDefinition[]
}

export class Field {
  diagram: Diagram
  fieldGroup: FieldGroup

  id: string
  fullId: string
  type: 'text' | 'boolean'
  title: string
  entityTypes: EntityTypes
  relationTypes: RelationTypes
  dataKey: string
  getInitialValue?: (data: Record<string, any>) => any
  formatter?: (value: any) => any
  isEntityLabel: boolean
  isRelationLabel: boolean
  addToLegend: boolean
  legendColor: string | null
  startActive: boolean
  display: boolean
  searchable: boolean
  editable: boolean

  constructor (
    diagram: Diagram,
    fieldGroup: FieldGroup,
    options: FieldDefinition,
  ) {
    this.diagram = diagram
    this.fieldGroup = fieldGroup

    this.id = options.id
    this.fullId = `${fieldGroup.id}.${this.id}`
    this.type = options.type ?? 'text'
    this.title = options.title
    this.entityTypes = diagram.entityTypes.pick(options.entityTypes ?? [])
    this.relationTypes = diagram.relationTypes.pick(options.relationTypes ?? [])
    this.dataKey = options.dataKey
    this.getInitialValue = options.getInitialValue
    this.formatter = options.formatter
    this.isEntityLabel = options.isEntityLabel ?? false
    this.isRelationLabel = options.isRelationLabel ?? false
    this.addToLegend = options.addToLegend ?? false
    this.legendColor = options.legendColor ?? null
    this.startActive = options.startActive ?? false
    this.display = options.display ?? true
    this.searchable = options.searchable ?? false
    this.editable = options.editable ?? true
  }

  get active () {
    return this.diagram.activeDiagram.settings.activeFields[this.fullId] ?? this.startActive
  }

  set active (active) {
    if (this.diagram.activeDiagram.settings.activeFields[this.fullId] === active) return

    this.diagram.activeDiagram.settings.activeFields[this.fullId] = active
    this.diagram.eventBus.emit({ name: 'settingsChanged' })
    this.diagram.eventBus.emit({ name: 'activeFieldsChanged' })
  }

  getValue (data: Record<string, any>) {
    let value = getDeep(data, this.dataKey)
    if (value === null || value === undefined) {
      value = this.getInitialValue?.(data)
      setDeep(data, this.dataKey, value)
    }
    return value
  }
}

export interface FieldDefinition {
  id: string
  type?: 'text' | 'boolean'
  title: string
  entityTypes?: string[]
  relationTypes?: string[]
  dataKey: string
  getInitialValue?: (data: Record<string, any>) => any
  formatter?: (value: any) => any
  isEntityLabel?: boolean
  isRelationLabel?: boolean
  addToLegend?: boolean
  legendColor?: string
  startActive?: boolean
  display?: boolean
  searchable?: boolean
  editable?: boolean
}

export class Filter {
  diagram: Diagram

  id: string
  title: string
  filter: (context: FilterContext) => any
  filterWhen: 'active' | 'inactive'
  startActive: boolean

  constructor (diagram: Diagram, options: FilterDefinition) {
    this.diagram = diagram

    this.id = options.id
    this.title = options.title
    this.filter = options.filter ?? (() => true)
    this.filterWhen = options.filterWhen ?? 'inactive'
    this.startActive = options.startActive ?? true
  }

  get active () {
    return this.diagram.activeDiagram.settings.activeFilters[this.id] ?? this.startActive
  }

  set active (active) {
    if (this.diagram.activeDiagram.settings.activeFilters[this.id] === active) return

    this.diagram.activeDiagram.settings.activeFilters[this.id] = active
    this.diagram.eventBus.emit({ name: 'settingsChanged' })
    this.diagram.eventBus.emit({ name: 'activeFiltersChanged' })
  }
}

export interface FilterDefinition {
  id: string
  title: string
  filter: (context: FilterContext) => any
  filterWhen?: 'active' | 'inactive'
  startActive?: boolean
}

export interface FilterContext {
  isEntity: boolean
  isRelation: boolean
  type: string
  data: Record<string, any>
  isMainEntity: boolean
  isParent: boolean
  isChild: boolean
}

export interface LayoutDefinition {
  id: string
  name: string // The name of the layout
  default?: boolean
  directed?: boolean // whether the tree is directed downwards (or edges can point in any direction if false)
  inverted?: boolean
  grid?: boolean // whether to create an even grid into which the DAG is placed (circle:false only)
  spacingFactor?: number // positive spacing factor, larger => more space between nodes (N.B. n/a if causes overlap)
  maximal?: boolean // whether to shift nodes down their natural BFS depths in order to avoid upwards edges (DAGS only)
  animate?: boolean // whether to transition the node positions
  animationDuration?: number // duration of animation in ms if enabled
  animationEasing?: string // easing of animation if enabled,
}

export class Settings {
  diagram: Diagram

  initialState: Pick<Settings, 'activeFields' | 'activeFilters' | 'activeLayout'> = {
    activeFields: {},
    activeFilters: {},
    activeLayout: null,
  }

  activeFields: Record<string, boolean | undefined> = {}
  activeFilters: Record<string, boolean | undefined> = {}
  activeLayout: LayoutDefinition | null = null

  constructor (diagram: Diagram) {
    this.diagram = diagram

    this.init()

    this.diagram.eventBus.on('settingsChanged', () => {
      this.saveToLocalStorage()
    })
  }

  init () {
    this.activeFields = {}
    this.activeFilters = {}

    for (const field of this.diagram.fields) {
      this.activeFields[field.fullId] = field.startActive
    }

    for (const filter of this.diagram.filters) {
      this.activeFilters[filter.id] = filter.startActive
    }

    this.activeLayout = this.diagram.layouts.find(l => l.default) ?? this.diagram.layouts[0] ?? null

    this.loadFromLocalStorage()
    this.saveToLocalStorage()

    this.initialState.activeFields = { ...this.activeFields }
    this.initialState.activeFilters = { ...this.activeFilters }
    this.initialState.activeLayout = this.activeLayout
  }

  reset () {
    this.activeFields = { ...this.initialState.activeFields }
    this.activeFilters = { ...this.initialState.activeFilters }
    this.activeLayout = this.initialState.activeLayout
  }

  loadFromLocalStorage () {
    if (!this.diagram.saveSettingsToLocalStorage) return

    try {
      const json = localStorage.getItem('lassox-diagram/settings')
      if (!json) return

      const settings = JSON.parse(json)
      if (!isRecord(settings)) return

      const { activeFields, activeFilters, activeLayout } = settings

      if (isRecord(activeFields)) {
        for (const field of this.diagram.fields) {
          const active = activeFields[field.fullId]
          if (typeof active == 'boolean') this.activeFields[field.fullId] = active
        }
      }

      if (isRecord(activeFilters)) {
        for (const filter of this.diagram.filters) {
          const active = activeFilters[filter.id]
          if (typeof active == 'boolean') this.activeFilters[filter.id] = active
        }
      }

      if (typeof activeLayout === 'string') {
        const layout = this.diagram.layouts.find(l => l.id == activeLayout)
        if (layout) this.activeLayout = layout
      }
    } catch {
      return
    }
  }

  saveToLocalStorage () {
    if (!this.diagram.saveSettingsToLocalStorage) return

    try {
      localStorage.setItem('lassox-diagram/settings', JSON.stringify({
        activeFields: this.activeFields,
        activeFilters: this.activeFilters,
        activeLayout: this.activeLayout?.id,
      }))
    } catch {
      return
    }
  }
}

export * from './example'
