












































import { Vue, Component, Prop, Inject } from 'vue-property-decorator'
import { EntityType } from '@/diagram'
import DiagramVue from './Diagram.vue'
import EditorFields from './EditorFields.vue'
import EditorField from './EditorField.vue'
import DataCreator from './DataCreator.vue'
import FieldContainer from './FieldContainer.vue'
import TabBar from './TabBar.vue'
import DropdownField from './DropdownField.vue'
import EntitySearchBar, { Suggestion } from './EntitySearchBar.vue'
import _set from 'lodash.set'

export type EntitySelectorModel = (
  {
    entityType: EntityType
  } & (
    {
      mode: 'search'
      selectedSuggestion: Suggestion
    } | {
      mode: 'custom'
      entityData: Record<string, any>
    }
  )
) | null

@Component({
  components: {
    EntitySearchBar,
    EditorFields,
    EditorField,
    DataCreator,
    FieldContainer,
    TabBar,
    DropdownField,
  },
})

export default class EntitySelector extends Vue {
  @Inject()
  readonly diagramVm!: DiagramVue

  @Prop(Array)
  readonly entityTypes?: EntityType[]

  entityType: EntityType | null = null
  mode: 'search' | 'custom' = 'search'
  selectedSuggestion: Suggestion | null = null
  entityData: Record<string, any> = {}

  get diagram () {
    return this.diagramVm.diagram
  }

  get finalEntityTypes () {
    return this.entityTypes || this.diagram.entityTypes.items
  }

  get searchEnabled () {
    return this.entityType?.searchable ?? false
  }

  get entityTypeDropdownOptions () {
    return this.finalEntityTypes.map(type => ({
      id: type.id,
      label: type.labels.singular,
      value: type,
    }))
  }

  get entityFields () {
    return this.entityType?.fields.filter(field => field.isEntityLabel) ?? []
  }

  updateModel () {
    const { entityType, mode, selectedSuggestion, entityData } = this

    const model = ((): EntitySelectorModel => {
      if (!entityType) return null

      if (mode === 'search') {
        if (!selectedSuggestion) return null
        return {
          entityType,
          mode,
          selectedSuggestion,
        }
      } else {
        return {
          entityType,
          mode,
          entityData,
        }
      }
    })()

    this.$emit('change', model)
  }

  created () {
    this.$watch(
      () => this.entityTypes,
      () => {
        if (this.entityType && this.finalEntityTypes.includes(this.entityType)) return
        this.entityType = this.finalEntityTypes[0] ?? null
      },
      { immediate: true },
    )

    this.$watch(
      () => this.entityType,
      () => {
        this.selectedSuggestion = null
        this.entityData = {}
      },
      { immediate: true },
    )

    this.$watch(
      () => this.mode,
      () => {
        this.selectedSuggestion = null
        this.entityData = {}
      },
      { immediate: true },
    )

    this.$watch(
      () => this.searchEnabled,
      () => {
        if (!this.searchEnabled) this.mode = 'custom'
      },
      { immediate: true },
    )

    this.$watch(
      () => [this.entityType, this.mode, this.selectedSuggestion],
      () => {
        this.updateModel()
      },
      { immediate: true },
    )
  }
}
