import Diagram, { DiagramConfig } from '@/diagram'

const LassoDiagram = {
  init: (config: DiagramConfig) => {
    const diagram = new Diagram(config)

    return {
      render: (container: HTMLElement) => diagram.render(container),
    }
  },
}

export default LassoDiagram
export { LassoDiagram as Diagram }

export { exampleConfig, exampleData } from '@/diagram'
