






































import { Vue, Component, Prop, Watch } from 'vue-property-decorator'
import BaseButton from './BaseButton.vue'
import Diagram, { FieldGroup, Field } from '@/diagram'
import { watchRect, unwatchRect } from '@/util/watch-rect'

@Component({
  components: {
    BaseButton,
  },
})

export default class CustomizerFieldGroupDropdownButton extends Vue {
  @Prop({ type: Diagram, required: true })
  readonly diagram!: Diagram

  @Prop({ type: Object, required: true })
  readonly fieldGroup!: FieldGroup

  showDropdownMenu = false

  els: Record<'buttonEl' | 'dropdownMenuEl' | 'boundsEl', HTMLElement> | null = null
  buttonRect: DOMRect | null = null
  boundsRect: DOMRect | null = null

  get dropdownStyle () {
    const { buttonRect, boundsRect } = this
    if (!buttonRect || !boundsRect) return null

    return {
      width: `${buttonRect.width}px`,
      maxHeight: `${boundsRect.bottom - buttonRect.bottom}px`,
      transform: (
        `translateX(${buttonRect.left}px)`
        + `translateY(${buttonRect.bottom}px)`
      ),
    }
  }

  get fields () {
    return this.fieldGroup.fields.filter(field => field.display)
  }

  mounted () {
    this.els = {
      buttonEl: (this.$refs.button as Vue).$el as HTMLElement,
      dropdownMenuEl: this.$refs.dropdownMenuEl as HTMLElement,
      boundsEl: this.$refs.boundsEl as HTMLElement,
    }
  }

  beforeDestroy () {
    this.showDropdownMenu = false
  }

  updateButtonRect (rect: DOMRect) {
    this.buttonRect = rect
  }

  updateBoundsRect (rect: DOMRect) {
    this.boundsRect = rect
  }

  onFocusOut (event: FocusEvent) {
    if (!this.els) return

    const { buttonEl, dropdownMenuEl } = this.els

    const newFocusEl = event.relatedTarget

    if (newFocusEl instanceof HTMLElement) {
      if (buttonEl.contains(newFocusEl)) return
      if (dropdownMenuEl.contains(newFocusEl)) return
    }

    this.showDropdownMenu = false
  }

  @Watch('showDropdownMenu', { immediate: true })
  onShowDropdownMenuChanged () {
    if (!this.els) return

    const { buttonEl, boundsEl } = this.els

    if (this.showDropdownMenu) {
      watchRect(buttonEl, this.updateButtonRect)
      watchRect(boundsEl, this.updateBoundsRect)
    } else {
      unwatchRect(buttonEl, this.updateButtonRect)
      unwatchRect(boundsEl, this.updateBoundsRect)
    }
  }
}
