










































import { Vue, Component, Inject, Prop } from 'vue-property-decorator'
import Dialog from './Dialog.vue'
import DataCreator from './DataCreator.vue'
import FieldContainer from './FieldContainer.vue'
import DropdownField from './DropdownField.vue'
import TextField from './TextField.vue'
import EntitySelector, { EntitySelectorModel } from './EntitySelector.vue'
import EntitySearchBar from './EntitySearchBar.vue'
import EditorFieldGroups from './EditorFieldGroups.vue'
import EditorFieldGroup from './EditorFieldGroup.vue'
import EditorField from './EditorField.vue'
import TabBar from './TabBar.vue'
import DropdownMenuDivider from './DropdownMenuDivider.vue'
import ButtonGroup from './ButtonGroup.vue'
import Button from './Button.vue'
import DiagramVue from './Diagram.vue'
import { Entity, EntityDefinition, EntityType, RelationDefinition, RelationType } from '@/diagram'
import _set from 'lodash.set'
import _cloneDeep from 'lodash.clonedeep'
import _merge from 'lodash.merge'

@Component({
  components: {
    Dialog,
    DataCreator,
    FieldContainer,
    DropdownField,
    TextField,
    EntitySelector,
    EntitySearchBar,
    EditorFieldGroups,
    EditorFieldGroup,
    EditorField,
    TabBar,
    DropdownMenuDivider,
    ButtonGroup,
    Button,
  },
})

export default class AddRelationDialog extends Vue {
  @Inject()
  readonly diagramVm!: DiagramVue

  @Prop({ type: RelationType, required: true })
  readonly relationType!: RelationType

  @Prop({ type: Entity, required: true })
  readonly entity!: Entity

  @Prop({ type: Boolean, required: true })
  readonly asParent!: boolean

  @Prop(Function)
  readonly onConfirm?: () => void

  @Prop(Function)
  readonly onDismiss?: () => void

  isConfirming = false

  relationData: Record<string, any> = {}

  entitySelectorModel: EntitySelectorModel = null

  get diagram () {
    return this.diagramVm.diagram
  }

  get relationTypeLabel () {
    const { labels } = this.relationType
    return (
      (this.asParent ? labels.singularFrom : labels.singularTo)
      ?? labels.singular
    )
  }

  get relationFields () {
    return this.relationType.fields.filter(field => field.isRelationLabel)
  }

  get supportedEntityTypes () {
    const types = new Set<EntityType>()

    for (const support of this.relationType.supports) {
      if (this.asParent) {
        if (support.to !== this.entity.type.id) continue
      } else {
        if (support.from !== this.entity.type.id) continue
      }

      const type = this.diagram.entityTypes.get(this.asParent ? support.from : support.to)
      types.add(type)
    }

    return [ ...types ]
  }

  get canConfirm () {
    if (this.isConfirming) return false
    if (!this.entitySelectorModel) return false
    return true
  }

  close () {
    this.$emit('close')
  }

  async confirm () {
    const { canConfirm, entitySelectorModel } = this

    if (!canConfirm || !entitySelectorModel) return

    this.isConfirming = true

    const generateId = (prefix: string, items: Map<string, any>) => {
      const time = Date.now()
      let i = 0
      let id = ''
      while (!id || items.has(id)) {
        id = `${prefix}-${time}-${i}`
        i++
      }
      return id
    }

    const generateData = (fields: ({
      dataKey?: string,
      value: any,
    })[]) => {
      const data: Record<string, any> = {}
      for (const field of fields) {
        if (field.dataKey) _set(data, field.dataKey, field.value)
      }
      return data
    }

    let entityId: string
    let newEntity: EntityDefinition | null = null

    if (entitySelectorModel.mode === 'search') {
      const suggestion = entitySelectorModel.selectedSuggestion

      entityId = suggestion.id

      if (suggestion.entityDefinition) {
        newEntity = _cloneDeep(suggestion.entityDefinition)

        if (this.diagram.methods.getEntityData) {
          const entity = (await this.diagram.methods.getEntityData({
            ids: [ suggestion.id ],
            relationTypes: [],
            parents: 0,
            children: 0,
          })).entities?.find(e => e.id === suggestion.id)

          if (entity) newEntity = _merge(newEntity, entity)
        }
      }
    } else {
      entityId = generateId('CUSTOM-ENTITY', this.diagram.activeDiagram.data.entitiesMap)

      newEntity = {
        type: entitySelectorModel.entityType.id,
        id: entityId,
        data: entitySelectorModel.entityData,
      }
    }

    const newRelation: RelationDefinition = {
      type: this.relationType.id,
      id: generateId('CUSTOM-RELATION', this.diagram.activeDiagram.data.relationsMap),
      data: this.relationData,
      from: this.asParent ? entityId : this.entity.id,
      to: this.asParent ? this.entity.id : entityId,
    }

    this.diagram.activeDiagram.data.add({
      entities: newEntity ? [ newEntity ] : undefined,
      relations: [ newRelation ],
    })

    this.close()
    this.onConfirm?.()
  }

  dismiss () {
    this.close()
    this.onDismiss?.()
  }
}
