import Diagram, { Entity, EntityDefinition, EntityStyle, Relation, RelationDefinition, RelationStyle } from '@/diagram'
import { merge } from '@/util/revertible-object-manipulation'

export default class DiagramData {
  constructor (diagram: Diagram, options?: {
    data?: DiagramDataDefinition
    changes?: Change[]
  }) {
    this.diagram = diagram

    if (options) {
      this.initialData = {
        entities: options.data?.entities?.map(item => new Entity(this.diagram, item)) ?? [],
        relations: options.data?.relations?.map(item => new Relation(this.diagram, item)) ?? [],
        changes: options.changes ?? [],
      }

      this.entitiesMap.clear()
      this._addEntities(this.initialData.entities)

      this.relationsMap.clear()
      this._addRelations(this.initialData.relations)

      for (const change of this.initialData.changes) {
        this.commitChange(change, true)
      }
    }
  }

  diagram: Diagram

  initialData: {
    entities: Entity[]
    relations: Relation[]
    changes: Change[]
  } = {
    entities: [],
    relations: [],
    changes: [],
  }

  changes: CompiledChange[] = []
  undoneChanges: CompiledChange[] = []

  entitiesMap = new Map<string, Entity>()
  entities: Entity[] = []

  relationsMap = new Map<string, Relation>()
  relations: Relation[] = []

  get mainEntity () {
    return this.entitiesMap.get(this.diagram.mainEntityId)
  }

  get canUndo () {
    return this.changes.length > 0
  }

  get canRedo () {
    return this.undoneChanges.length > 0
  }

  add (options: Exclude<Change['add'], undefined>) {
    this.commitChange({
      add: {
        entities: options.entities,
        relations: options.relations,
      },
    })
  }

  remove (options: Exclude<Change['remove'], undefined>) {
    this.commitChange({
      remove: {
        entities: options.entities,
        relations: options.relations,
      },
    })
  }

  edit (options: Exclude<Change['edit'], undefined>) {
    this.commitChange({
      edit: {
        entities: options.entities,
        relations: options.relations,
      },
    })
  }

  commitChange (change: Change, isInitial = false) {
    const compiledChange = this._compileChange(change)

    if (compiledChange.apply(!isInitial) == false) return

    if (!isInitial) {
      this.changes.push(compiledChange)
      this.undoneChanges = []
    }
  }

  undo (trackEvents = true) {
    const lastChange = this.changes.pop()
    if (!lastChange) return

    lastChange.revert(trackEvents)
    this.undoneChanges.push(lastChange)

    if (trackEvents) this.diagram.eventBus.emit({ name: 'undo' })
  }

  redo (trackEvents = true) {
    const lastUndoneChange = this.undoneChanges.pop()
    if (!lastUndoneChange) return

    lastUndoneChange.apply(trackEvents)
    this.changes.push(lastUndoneChange)

    if (trackEvents) this.diagram.eventBus.emit({ name: 'redo' })
  }

  reset () {
    while (this.canUndo) this.undo(false)

    this.changes = []
    this.undoneChanges = []
  }

  private _compileChange (change: Change): CompiledChange {
    let applied = false
    const applyFns: (() => void | false)[] = []
    const revertFns: (() => void)[] = []

    if (change.add) {
      const addition = change.add

      applyFns.push(() => {
        addition.entities = addition.entities?.filter(e => !this.entitiesMap.has(e.id))
        addition.relations = addition.relations?.filter(r => !this.relationsMap.has(r.id))

        const entities = mapThrowable(addition.entities ?? [], entity => new Entity(this.diagram, entity))
        const relations = mapThrowable(addition.relations ?? [], relation => new Relation(this.diagram, relation))

        // Cancel if nothing to add
        if (!entities.length && !relations.length) return false

        this._addEntities(entities)
        this._addRelations(relations)

        revertFns.push(() => {
          this._removeEntities(entities)
          this._removeRelations(relations)
        })
      })
    }

    if (change.remove) {
      const removal = change.remove

      applyFns.push(() => {
        const entities: Entity[] = []
        const relations: Relation[] = []

        for (const id of removal.entities ?? []) {
          const entity = this.entitiesMap.get(id)
          if (entity) entities.push(entity)
        }

        for (const id of removal.relations ?? []) {
          const relation = this.relationsMap.get(id)
          if (relation) relations.push(relation)
        }

        removal.entities = removal.entities && entities.map(e => e.id)
        removal.relations = removal.relations && relations.map(r => r.id)

        // Cancel if nothing to remove
        if (!entities.length && !relations.length) return false

        if (entities.length) this._removeEntities(entities)
        if (relations.length) this._removeRelations(relations)

        revertFns.push(() => {
          if (entities.length) this._addEntities(entities)
          if (relations.length) this._addRelations(relations)
        })
      })
    }

    if (change.edit) {
      const edit = change.edit

      applyFns.push(() => {
        // These will replace change.entities and change.relations, so unused edits are removed
        const entityEdits: EntityEdit[] | undefined = edit.entities && []
        const relationEdits: RelationEdit[] | undefined = edit.relations && []

        const reverters: (() => void)[] = []

        for (const entityEdit of edit.entities ?? []) {
          const entity = this.entitiesMap.get(entityEdit.id)
          if (!entity) continue

          entityEdits?.push(entityEdit)
          reverters.push(merge(entity, {
            data: entityEdit.data ?? {},
            style: entityEdit.style ?? {},
          }))
        }

        for (const relationEdit of edit.relations ?? []) {
          const relation = this.relationsMap.get(relationEdit.id)
          if (!relation) continue

          relationEdits?.push(relationEdit)
          reverters.push(merge(relation, {
            data: relationEdit.data ?? {},
            style: relationEdit.style ?? {},
          }))
        }

        edit.entities = entityEdits
        edit.relations = relationEdits

        // Cancel if nothing was edited
        if (!reverters.length) return false

        revertFns.push(...reverters)
      })
    }

    if (change.layout) {
      const layoutId = change.layout
      const oldLayout = this.diagram.activeDiagram.settings.activeLayout
      const newLayout = this.diagram.layouts.find(layout => layout.id === layoutId) ?? null

      applyFns.push(() => {
        if (!newLayout) return false

        this.diagram.activeDiagram.settings.activeLayout = newLayout

        revertFns.push(() => {
          this.diagram.activeDiagram.settings.activeLayout = oldLayout
        })
      })
    }

    if (change.moveEntities) {
      const moveEntities = change.moveEntities

      applyFns.push(() => {
        const reverters: (() => void)[] = []

        for (const { id, x, y } of moveEntities) {
          const entity = this.entitiesMap.get(id)
          if (!entity) continue

          reverters.push(merge(entity, {
            position: { x, y },
          }))
        }

        // Cancel if nothing was changed
        if (!reverters.length) return false

        revertFns.push(...reverters)
      })
    }

    return {
      change,
      apply: (trackEvents = true) => {
        if (applied) {
          console.warn('change.apply() was called after change was already applied')
          return false
        }

        applied = true

        let success = true
        for (const fn of applyFns) {
          if (fn() === false) {
            success = false
          }
        }

        if (trackEvents) {
          this.diagram.eventBus.emit({ name: 'dataUpdated' })
          if (change.layout) {
            this.diagram.eventBus.emit({ name: 'settingsChanged' })
            this.diagram.eventBus.emit({ name: 'graph.fit' })
          }
        }

        return success
      },
      revert: (trackEvents = true) => {
        if (!applied) {
          console.warn('change.revert() was called before change was applied')
          return
        }

        applied = false

        for (const fn of [...revertFns].reverse()) {
          fn()
        }

        revertFns.splice(0)

        if (trackEvents) {
          this.diagram.eventBus.emit({ name: 'dataUpdated' })
          if (change.layout) {
            this.diagram.eventBus.emit({ name: 'settingsChanged' })
            this.diagram.eventBus.emit({ name: 'graph.fit' })
          }
        }
      },
    }
  }

  private _addEntities (entities: Entity[]) {
    if (!entities.length) return
    for (const entity of entities) this.entitiesMap.set(entity.id, entity)
    this._updateEntitiesArray()
  }

  private _addRelations (relations: Relation[]) {
    if (!relations.length) return
    for (const relation of relations) this.relationsMap.set(relation.id, relation)
    this._updateRelationsArray()
  }

  private _removeEntities (entities: Entity[]) {
    if (!entities.length) return
    for (const entity of entities) this.entitiesMap.delete(entity.id)
    this._updateEntitiesArray()
  }

  private _removeRelations (relations: Relation[]) {
    if (!relations.length) return
    for (const relation of relations) this.relationsMap.delete(relation.id)
    this._updateRelationsArray()
  }

  private _updateEntitiesArray () {
    this.entities = [...this.entitiesMap.values()]
  }

  private _updateRelationsArray () {
    this.relations = [...this.relationsMap.values()]
  }
}

export interface DiagramDataDefinition {
  entities?: EntityDefinition[]
  relations?: RelationDefinition[]
}

export interface CompiledChange {
  change: Change
  apply: (trackEvents?: boolean) => boolean
  revert: (trackEvents?: boolean) => void
}

export interface Change {
  add?: {
    entities?: EntityDefinition[]
    relations?: RelationDefinition[]
  }
  remove?: {
    entities?: string[]
    relations?: string[]
  }
  edit?: {
    entities?: EntityEdit[]
    relations?: RelationEdit[]
  }
  layout?: string
  moveEntities?: EntityMove[]
}

export interface EntityEdit {
  id: string
  data?: Record<string, any>
  style?: EntityStyle
}

export interface RelationEdit {
  id: string
  data?: Record<string, any>
  style?: RelationStyle
}

export interface EntityMove {
  id: string
  x: number
  y: number
}

// Map an array and skip items that throw an exception.
const mapThrowable = <F, T> (items: F[], transformer: (item: F) => T): T[] => {
  const result: T[] = []
  for (const item of items) {
    try {
      result.push(transformer(item))
    } catch (e) {
      continue
    }
  }
  return result
}
