











































import { Vue, Component, Ref, Model } from 'vue-property-decorator'
import BaseButton from './BaseButton.vue'
import TextField from './TextField.vue'
import iro from '@jaames/iro'
import { watchRect } from '@/util/watch-rect'

interface InputField {
  name: string
  value: any
  setter?: InputFieldSetter
  on: any
}

type InputFieldSetter = (value: string) => iro.Color

@Component({
  components: {
    BaseButton,
    TextField,
  },
})

export default class ColorPicker extends Vue {
  @Model('change', { type: iro.Color, required: true })
  readonly color!: iro.Color

  @Ref()
  readonly iroEl!: HTMLElement

  inputFocused = false

  inputTypes = [
    'hex' as const,
    'rgb' as const,
    'hsl' as const,
  ]

  inputFields: InputField[] = []

  get inputType () {
    return this.inputTypes[0]
  }

  destroy?: () => void

  mounted () {
    let width = 0

    const { unwatch: unwatchRect } = watchRect(this.iroEl, rect => {
      width = rect.width
      if (colorPicker) colorPicker.resize(width)
    })

    const colorPicker = iro.ColorPicker(this.iroEl, {
      width,
      color: this.color,
      padding: 0,
      margin: 8,
      handleRadius: 7,
      sliderSize: 12,
      layout: [
        {
          component: iro.ui.Box,
          options: {
            boxHeight: 100,
          },
        },
        {
          component: iro.ui.Slider,
          options: {
            sliderType: 'hue',
          },
        },
      ],
    })

    this.$watch(() => this.color, () => {
      if (colorPicker.color.hslaString !== this.color.hslaString) colorPicker.color.set(this.color)
      if (!this.inputFocused) this.updateInputFields()
    })

    colorPicker.on('color:change', (color: iro.Color) => {
      this.$emit('change', color)
    })

    this.$watch(() => this.inputFocused, () => {
      if (!this.inputFocused) window.requestAnimationFrame(() => {
        if (!this.inputFocused) {
          this.updateInputFields()
        }
      })
    })

    this.updateInputFields()

    this.destroy = () => {
      unwatchRect()

      if (colorPicker.base) this.iroEl.removeChild(colorPicker.base)
      Object.assign(colorPicker, { el: null })
    }
  }

  beforeDestroy () {
    this.destroy?.()
  }

  switchInputType () {
    this.inputTypes = [
      ...this.inputTypes.slice(1),
      this.inputType,
    ]

    this.updateInputFields()
  }

  updateInputFields () {
    const fields: InputField[] = []

    const parseNumber = (value: string) => {
      const int = parseInt(value)
      if (isNaN(int)) return 0
      return int
    }

    switch (this.inputType) {
      case 'hex': {
        fields.push(
          {
            name: 'hex',
            value: this.color.hexString.slice(1),
            setter: value => new iro.Color(value),
            on: {},
          },
        )
        break
      }

      case 'rgb': {
        fields.push(
          {
            name: 'r',
            value: this.color.rgb.r,
            setter: value => {
              const color = this.color.clone()
              color.setChannel('rgb', 'r', parseNumber(value))
              return color
            },
            on: {},
          },
          {
            name: 'g',
            value: this.color.rgb.g,
            setter: value => {
              const color = this.color.clone()
              color.setChannel('rgb', 'g', parseNumber(value))
              return color
            },
            on: {},
          },
          {
            name: 'b',
            value: this.color.rgb.b,
            setter: value => {
              const color = this.color.clone()
              color.setChannel('rgb', 'b', parseNumber(value))
              return color
            },
            on: {},
          },
        )
        break
      }

      case 'hsl': {
        fields.push(
          {
            name: 'h',
            value: this.color.hsl.h,
            setter: value => {
              const color = this.color.clone()
              color.setChannel('hsl', 'h', parseNumber(value))
              return color
            },
            on: {},
          },
          {
            name: 's',
            value: this.color.hsl.s,
            setter: value => {
              const color = this.color.clone()
              color.setChannel('hsl', 's', parseNumber(value))
              return color
            },
            on: {},
          },
          {
            name: 'l',
            value: this.color.hsl.l,
            setter: value => {
              const color = this.color.clone()
              color.setChannel('hsl', 'l', parseNumber(value))
              return color
            },
            on: {},
          },
        )
        break
      }
    }

    for (const field of fields) {
      if (field.setter) {
        const { setter } = field

        field.setter = value => {
          try {
            const color = setter(value)
            this.$emit('change', color)
            return color
          } catch {
            return this.color
          }
        }
      }
    }

    this.inputFields = fields
  }
}
