













































import { Vue, Component, Prop, Model, Watch } from 'vue-property-decorator'
import Icon from './Icon.vue'

@Component({
  components: {
    Icon,
  },
})

export default class RadioField extends Vue {
  @Prop(String)
  readonly name?: string

  @Prop({ type: Boolean, default: false })
  readonly disabled!: boolean

  @Prop({ type: Array, required: true })
  readonly options!: ({
    label: string
    value: any
  })[]

  @Model('change')
  readonly value!: any

  finalOptions: ({
    label: string
    value: any
    focused: boolean
  })[] = []

  @Watch('options', { immediate: true })
  onOptionsChanged () {
    this.finalOptions = this.options.map(o => ({
      label: o.label,
      get value () {
        return o.value
      },
      set value (value) {
        o.value = value
      },
      focused: false,
    }))
  }
}
