function isObject (value: any) {
  const type = typeof value
  return value != null && (type === 'object' || type === 'function')
}

/**
 * Merges source object into target object and returns a revert function.
 */
export function merge (target: any, source: any) {
  if (!isObject(target)) throw Error(`target (${target}) is not an object`)
  if (!isObject(source)) throw Error(`source (${source}) is not an object`)
  if (target === source) return () => {}

  const reverters: (() => void)[] = []
  const commit = (revert: () => void) => reverters.unshift(revert)

  for (const key in source) {
    const targetValue = target[key]
    const sourceValue = source[key]

    if (isObject(sourceValue)) {
      if (isObject(targetValue)) {
        commit(merge(targetValue, sourceValue))
      } else {
        commit(set(target, key, sourceValue))
      }
    } else {
      commit(set(target, key, sourceValue))
    }
  }

  return () => reverters.forEach(revert => revert())
}

/**
 * Sets the value of an object property and returns a revert function.
 */
export function set (object: any, key: string, value: any) {
  if (!isObject(object)) throw Error(`(${object}) is not an object`)

  const oldValue = object[key]
  object[key] = value

  return () => object[key] = oldValue
}
