















import { Vue, Component, Prop, Ref, Inject } from 'vue-property-decorator'
import Diagram, { Entity, EntityStyle, Field, FilterContext, LayoutDefinition, Relation } from '@/diagram'
import cytoscape from 'cytoscape'
import panzoom from 'cytoscape-panzoom'
import nodeHtmlLabel from 'cytoscape-node-html-label'
import CytoscapeContextMenu from 'cytoscape-cxtmenu'
import 'cytoscape-panzoom/cytoscape.js-panzoom.css'
import { unwatchRect, watchRect } from '@/util/watch-rect'
import iro from '@jaames/iro'
import { getPercBrightness } from '@/util/color'
import { EntityMove } from '@/types/DiagramData'
import DiagramVue from './Diagram.vue'

// Show 'parent' and 'child' label on entities and relations
const debugHierarchy = false

const generateStyle = (vm: Graph): cytoscape.Stylesheet[] => {
  const getStyle = (element: cytoscape.NodeSingular | cytoscape.EdgeSingular, property: string, defaultValue?: any) => {
    const item = element.isNode() ? element.data('entity') : element.data('relation')
    return (
      item.style[property]
      ?? (vm.printMode ? item.type.printStyle?.[property] : null)
      ?? item.type.style[property]
      ?? defaultValue
    )
  }

  const getEdgeWidth = (edge: cytoscape.EdgeSingular): number => getStyle(edge, 'lineWidth')

  const getEdgeColor = (edge: cytoscape.EdgeSingular) => (
    (!vm.printMode && edge.selected()) ? '#cd4b3d' :
    getStyle(edge, 'lineColor')
  )

  return [
    {
      selector: 'node',
      style: {
        'width': (node: cytoscape.NodeSingular) => node.data('width'),
        'height': (node: cytoscape.NodeSingular) => node.data('height'),

        'background-opacity': 0,
        'text-valign': 'center',
        'shape': 'rectangle',
      },
    },

    {
      selector: 'edge',
      style: {
        'width': getEdgeWidth,

        'label': (edge: cytoscape.EdgeSingular) => {
          const relation: Relation = edge.data('relation')

          const strings: string[] = []

          if (debugHierarchy) {
            const labelStrings: string[] = []
            if (edge.data('isParent')) labelStrings.push('parent')
            if (edge.data('isChild')) labelStrings.push('child')
            if (labelStrings.length) strings.push(labelStrings.join(', '))
          }

          for (const fieldGroup of relation.fieldGroups) {
            for (const field of fieldGroup.fields) {
              if (!field.isRelationLabel) continue
              if (!field.active) continue

              const value = field.getValue(relation.data)
              const formatted = String(field.formatter ? field.formatter(value) : value ?? '')
              if (formatted) strings.push(formatted)
            }
          }

          return strings.join('\n')
        },

        'text-max-width': '200px',
        'text-wrap': 'wrap',

        'curve-style': 'bezier',
        // 'curve-style': 'taxi',

        'text-background-color': '#fff',
        'text-background-opacity': 1,

        'color': (edge: cytoscape.EdgeSingular) => (
          vm.printMode ? '#000' :
          edge.selected() ? '#cd4b3d' :
          '#777'
        ),
        'line-color': getEdgeColor,
        'source-arrow-color': getEdgeColor,
        'target-arrow-color': getEdgeColor,
        'source-arrow-fill': 'filled',
        'target-arrow-fill': 'filled',
        'arrow-scale': edge => getStyle(edge, 'arrowSize') / getEdgeWidth(edge),

        'line-style': edge => getStyle(edge, 'lineStyle') === 'solid' ? 'solid' : 'dashed',

        "line-dash-pattern": ((edge: any) => {
          const width = getEdgeWidth(edge)
          return getStyle(edge, 'lineStyle') === 'dotted' ? [width, width * 2] : [width * 2, width]
        }) as any,

        'source-arrow-shape': edge => getStyle(edge, 'arrowFrom') ? 'triangle-backcurve' : 'none',
        'target-arrow-shape': edge => getStyle(edge, 'arrowTo') ? 'triangle-backcurve' : 'none',

        'control-point-step-size': () => 100,
        'text-background-padding': () => '4px',

        // These don't exist in cytoscapes ts definitions
        ...{
          'loop-direction': () => '30deg',
          'loop-sweep': () => '45deg',
        },
      },
    },

    {
      selector: 'edge[?hidden], edge[?hiddenByFilter]',
      style: { 'visibility': 'hidden' },
    },
  ]
}

const initNodeHtmlLabel = (vm: Graph) => {
  vm._graph?.nodeHtmlLabel([{
    query: '.entity:inside', // very important to include :inside, as it will otherwise render removed to the graph in some cases.
    tpl: data => {
      const entity: Entity = data.entity

      const classes = [
        'lassox-diagram__Graph_entity',
        `lassox-diagram__Graph_entity--id-${data.id}`,
        entity.isMainEntity && 'lassox-diagram__Graph_entity--main-entity',
        !vm.printMode && data.selected && 'lassox-diagram__Graph_entity--selected',
      ].filter(v => !!v).join(' ')

      const createStyleString = (style: [string, unknown][]) => style.map(([prop, value], i) => {
        if (!value) return ''

        let string = ''
        if (i) string += ' '
        string += `${prop}: ${value};`
        return string
      }).join('')

      const getStyleValue = <P extends keyof EntityStyle> (prop: P) => (
        entity.style[prop]
        ?? (vm.printMode ? entity.type.printStyle?.[prop] : null)
        ?? entity.type.style[prop]
      )

      const getContrastingTextColor = (bgColor?: string) => getPercBrightness(new iro.Color(bgColor)) > 165
        ? 'rgba(0, 0, 0, 0.68)'
        : 'rgb(255, 255, 255)'

      const borderWidth = getStyleValue('borderWidth') ?? 2
      const borderStyle = getStyleValue('borderStyle')
      const borderColor = entity.isMainEntity ? '#000' : getStyleValue('borderColor')
      const bgColor = getStyleValue('backgroundColor') ?? '#ffffff'
      const labelsBgColor = getStyleValue('labelsBackgroundColor') ?? bgColor

      const style = createStyleString([
        ['visibility', (data.hidden || data.hiddenByFilter) ? 'hidden' : 'visible'],
        ['border-width', `${borderWidth}px`],
        ['border-style', borderStyle],
        ['border-color', borderColor],
      ])

      const selectedOutlineStyle = createStyleString([
        ['top', `${-borderWidth}px`],
        ['left', `${-borderWidth}px`],
        ['right', `${-borderWidth}px`],
        ['bottom', `${-borderWidth}px`],
      ])

      const labelsStyle = createStyleString([
        ['color', getContrastingTextColor(labelsBgColor)],
        ['background-color', labelsBgColor],
      ])

      const fieldsStyle = createStyleString([
        ['border-top-width', `${borderWidth}px`],
        ['border-style', borderStyle],
        ['border-color', borderColor],
        ['color', getContrastingTextColor(bgColor)],
        ['background-color', bgColor],
      ])

      const createHtmlField = (options: {
        field?: Field
        legendColor?: string
        text?: string
      }) => {
        const getFormattedFieldValue = (field: Field) => {
          const value = field.getValue(entity.data)
          if (field.formatter) return field.formatter(value)
          return String(value ?? '')
        }

        const encodeHTML = (string: string) => string.replace(/&/g, '&amp;').replace(/</g, '&lt;').replace(/"/g, '&quot;')

        let legendColor = options.legendColor ?? (
          options.field?.addToLegend
            ? options.field.legendColor
            : null
        )

        let text = options.text ?? (
          options.field
            ? getFormattedFieldValue(options.field)
            : ''
        )

        if (!text) return ''

        let html = ''
        html += '<div class="lassox-diagram__Graph_entity-field">'
        if (legendColor) html += `<div class="lassox-diagram__Graph_entity-field-legend-color" style="background-color: ${legendColor};"></div>`
        html += `<div class="lassox-diagram__Graph_entity-field-value">${encodeHTML(text)}</div>`
        html += '</div>'
        return html
      }

      const labels: string[] = []
      const fields: string[] = []

      if (debugHierarchy) {
        const labelStrings: string[] = []
        if (data.isParent) labelStrings.push('parent')
        if (data.isChild) labelStrings.push('child')
        if (labelStrings.length) labels.push(createHtmlField({ text: labelStrings.join(', ') }))
      }

      for (const fieldGroup of entity.fieldGroups) {
        for (const field of fieldGroup.fields) {
          if (!field.active) continue

          const htmlField = createHtmlField({ field })
          if (!htmlField) continue

          if (field.isEntityLabel) labels.push(htmlField)
          else fields.push(htmlField)
        }
      }

      if (!labels.length) {
        labels.push(createHtmlField({ text: entity.type.labels.singular }))
      }

      const labelsHtml = labels.join('')
      const fieldsHtml = fields.join('')

      return (
        `<div class="${classes}" style="${style}">` +
          `<div class="lassox-diagram__Graph_entity-selected-outline" style="${selectedOutlineStyle}"></div>` +

          `<div class="lassox-diagram__Graph_entity-labels-wrapper" style="${labelsStyle}">` +
            (entity.type.icon ? (
              `<div class="lassox-diagram__Graph_entity-icon material-icons-outlined">${entity.type.icon}</div>`
            ) : '') +

            '<div class="lassox-diagram__Graph_entity-labels">' +
              labelsHtml +
            '</div>' +
          '</div>' +

          (fieldsHtml ? (
            `<div class="lassox-diagram__Graph_entity-fields" style="${fieldsStyle}">` +
              fieldsHtml +
            '</div>'
          ) : '') +
        '</div>'
      )
    },
  }], {
    enablePointerEvents: true,
  })
}

panzoom(cytoscape)
nodeHtmlLabel(cytoscape)
cytoscape.use(CytoscapeContextMenu)

@Component({})

export default class Graph extends Vue {
  @Inject()
  readonly diagramVm!: DiagramVue

  @Prop({ type: Boolean, default: false })
  readonly printMode!: boolean

  @Ref()
  readonly graphContainerEl!: HTMLElement

  _graph?: cytoscape.Core

  addedItems = new Map<string, Entity | Relation>()

  onDestroy?: () => void

  get diagram () {
    return this.diagramVm.diagram
  }

  get el () {
    return this.$el as HTMLElement
  }

  get fitPadding () {
    return this.printMode ? 0 : 16
  }

  mounted () {
    const destroyCallbacks: (() => void)[] = []

    this.initialize()

    destroyCallbacks.push(watchRect(this.el, this.onResize).unwatch)

    if (!this.printMode) {
      destroyCallbacks.push(
        this.diagram.eventBus.on('graph.layout', ({ layout }) => {
          this.layout(layout, false)?.then(() => {
            const createMoveEntities = () => {
              const moveEntities: EntityMove[] = []
              this._graph?.nodes().forEach(node => {
                const { x, y } = node.position()
                moveEntities.push({ id: node.id(), x, y })
              })
              return moveEntities
            }

            this.diagram.activeDiagram.data.commitChange({
              layout: layout.id,
              moveEntities: createMoveEntities(),
            })
          })
        }),

        this.diagram.eventBus.on('graph.fit', () => this.fit()),

        this.diagram.eventBus.on([ 'dataUpdated', 'activeFieldsChanged' ], () => {
          this.updateElements()
          this.runFilters()
        }),

        this.diagram.eventBus.on('activeFiltersChanged', () => {
          this.runFilters()
        }),
      )

      this.$watch(() => this.diagram.activeDiagram, () => {
        this.clearElements()
        this.updateElements()
        this.runFilters()
      }, { immediate: true })
    } else {
      const updateElementsPromise = this.updateElements()
      this.runFilters()
      updateElementsPromise.then(() => {
        this.fit()
        this.$emit('readyToPrint')
      })
    }

    this.onDestroy = () => {
      for (const cb of destroyCallbacks) cb()
    }
  }

  beforeDestroy () {
    this.onDestroy?.()

    this._graph?.destroy()
    this._graph = undefined
  }

  // When clicking the graph container, cytoscape removes focus, so body ends up focused.
  // This focuses the graph container instead.
  redirectCytoscapeBlur (event: Event) {
    if (this.printMode || event.defaultPrevented) return

    setTimeout(() => {
      if (document.activeElement == null || document.activeElement == document.body) {
        (this.$el as HTMLElement).focus()
      }
    })
  }

  // Undo (ctrl+z) and redo (ctrl+y)
  onKeyDown (event: KeyboardEvent) {
    if (this.printMode || event.defaultPrevented) return

    if (event.ctrlKey || event.metaKey) {
      const key = event.key.toLowerCase()

      const action = (
        key == 'z' ? 'undo' :
        key == 'y' ? 'redo' :
        null
      )

      if (action) {
        event.preventDefault()

        switch (action) {
          case 'undo': {
            this.diagram.activeDiagram.data.undo()
            break
          }

          case 'redo': {
            this.diagram.activeDiagram.data.redo()
            break
          }
        }
      }
    }
  }

  onResize () {
    if (this._graph) {
      const { width, height } = this.el.getBoundingClientRect()
      if (width === this._graph.width() && height === this._graph.height()) return
      this._graph.resize()
    }
  }

  initialize () {
    // Initialize cytoscape
    const graph = this._graph = cytoscape({
      container: this.graphContainerEl,
      style: generateStyle(this),
      wheelSensitivity: 0.05,
      maxZoom: !this.printMode ? 1 : undefined,
    })

    // Initialize node-html-label
    initNodeHtmlLabel(this)

    if (!this.printMode) {
      // Initialize panzoom
      graph.panzoom({
        zoomOnly: true,
        fitSelector: '[!hiddenByFilter]',
        fitPadding: this.fitPadding,
        zoomInIcon: 'material-icons-outlined',
        zoomOutIcon: 'material-icons-outlined',
        resetIcon: 'material-icons-outlined',
        zoomFactor: 0.1,
      })
    }

    // Fit elements in graph when graph is resized
    graph.on('resize', () => {
      this.fit()
    })

    if (!this.printMode) {
      const isElement = (target: cytoscape.Core | cytoscape.SingularElementArgument): target is cytoscape.SingularElementArgument => {
        return 'isNode' in target && 'isEdge' in target && (target.isNode() || target.isEdge())
      }

      graph.on('click', event => {
        if (!isElement(event.target)) return
        this.diagram.eventBus.emit({
          name: 'editor.open',
          target: event.target.data(event.target.isNode() ? 'entity' : 'relation')
        })
      })

      graph.on('select', event => {
        if (!isElement(event.target)) return
        event.target.data('selected', true)
      })

      graph.on('unselect', event => {
        if (!isElement(event.target)) return
        event.target.data('selected', false)
      })

      // Track node position changes
      const movedNodes = new Set<cytoscape.NodeSingular>()

      graph.on('dragfree', event => {
        if (!isElement(event.target) || !event.target.isNode()) return

        movedNodes.add(event.target)

        if (movedNodes.size == 1) requestAnimationFrame(() => {
          this.diagram.activeDiagram.data.commitChange({
            moveEntities: [...movedNodes].map(node => {
              const position = node.position()
              return {
                id: node.id(),
                x: position.x,
                y: position.y,
              }
            })
          })

          movedNodes.clear()
        })
      })
    }
  }

  clearElements () {
    const graph = this._graph
    if (!graph) return

    graph.elements().remove()
    this.addedItems.clear()
  }

  updateElements () {
    return new Promise<void>(resolve => {
      const graph = this._graph
      if (!graph) return resolve()

      graph.batch(() => {
        const shouldLayout = !this.printMode && !graph.elements().length

        const items = new Map<string, Entity | Relation>([
          ...this.diagram.activeDiagram.data.entitiesMap,
          ...this.diagram.activeDiagram.data.relationsMap,
        ])

        const toAdd: (Entity | Relation)[] = []
        const toUpdate: (Entity | Relation)[] = []
        const toRemove: (Entity | Relation)[] = []

        for (const [id, item] of items) {
          if (!this.addedItems.has(id)) toAdd.push(item)
          else toUpdate.push(item)
        }

        for (const [id, item] of this.addedItems) {
          if (!items.has(id)) toRemove.push(item)
        }

        const addedElements = graph.add(toAdd.map(item => (
          item.isEntity
            ? {
              group: 'nodes',
              classes: (
                'entity' +
                (item.position ? '' : ' needs-position')
              ),
              grabbable: !this.printMode && this.diagram.enableDragAndDrop,
              position: item.position,
              data: {
                id: item.id,
                entity: item,
                width: 160,
                height: 36,
              },
            }
            : {
              group: 'edges',
              classes: 'relation',
              data: {
                id: item.id,
                relation: item,
                source: item.from,
                target: item.to,
              },
            }
        )))
        const addedNodes = addedElements.nodes()
        const addedEdges = addedElements.edges()

        const hiddenElements = graph.elements('.needs-position')
        hiddenElements.data('hidden', true)

        for (const item of toUpdate) {
          const element = graph.$id(item.id)

          element.data(item.isEntity ? 'entity' : 'relation', item)

          if (item.isEntity) {
            element.toggleClass('needs-position', !item.position)
            if (item.position) element.position(item.position)
          }
        }

        for (const item of toRemove) {
          graph.$id(item.id).remove()
        }

        const elements = graph.elements()
        const mainEntityNode = elements.$id(this.diagram.mainEntityId)

        this.addedItems = items

        // Calculate hierarchy
        elements.data({
          isParent: false,
          isChild: false,
        })

        mainEntityNode.predecessors().not(mainEntityNode).data('isParent', true)
        mainEntityNode.successors().not(mainEntityNode).data('isChild', true)

        const onDone = () => {
          graph.one('render', () => requestAnimationFrame(() => resolve()))

          graph.batch(() => {
            if (!this.printMode) {
              // Set initial entity positions
              graph.nodes().forEach(node => {
                const entity: Entity = node.data('entity')
                if (!entity.position) {
                  entity.initialState.position = { ...node.position() }
                  entity.position = { ...node.position() }
                  node.removeClass('needs-position')
                }
              })
            }

            // Fit graph to screen, if necessary
            if (shouldLayout) this.fit()

            // Unhide elements
            hiddenElements.data('hidden', false)
          })
        }

        if (addedNodes.length) {
          graph.one('render', () => {
            graph.batch(() => {
              addedNodes.forEach(node => {
                const nodeEl = this.el.querySelector(`.lassox-diagram__Graph_entity--id-${node.id()}`)?.parentElement
                if (!nodeEl) return

                const onRectChanged = (rect: DOMRect, oldRect: DOMRect | null) => {
                  if (oldRect && rect.width === oldRect.width && rect.height === oldRect.height) return

                  const zoom = this._graph?.zoom() ?? 1
                  const width = rect.width / zoom
                  const height = rect.height / zoom

                  const { width: oldWidth, height: oldHeight } = node.data()

                  if (width === oldWidth && height === oldHeight) return

                  node.data({
                    width,
                    height,
                  })
                }

                const unwatchRect = watchRect(nodeEl, onRectChanged).unwatch
                node.on('remove', unwatchRect)
              })
            })

            graph.one('render', () => {
              const nodesToLayout = shouldLayout ? graph.nodes('.needs-position') : null

              if (nodesToLayout?.length) {
                const nodesToLock = graph.nodes().not(nodesToLayout).not(':locked')
                nodesToLock.lock()

                this.layout(this.diagram.activeDiagram.settings.activeLayout, false)?.then(() => graph.batch(() => {
                  nodesToLock.unlock()
                  onDone()
                }))
              } else {
                onDone()
              }
            })
          })
        } else {
          onDone()
        }
      })
    })
  }

  runFilters () {
    const graph = this._graph
    if (!graph) return

    const filters = this.diagram.filters.filter(filter => filter.filterWhen === 'active' ? filter.active : !filter.active)

    graph.batch(() => {
      const elements = graph.elements()

      elements.data('hiddenByFilter', false)

      elements.forEach(element => {
        // Skip elements that are already hidden
        if (element.data('hiddenByFilter')) return

        const entityOrRelation: Entity | Relation = element.data(element.isNode() ? 'entity' : 'relation')
        const entity: Entity | null = entityOrRelation instanceof Entity ? entityOrRelation : null
        const relation: Relation | null = entityOrRelation instanceof Relation ? entityOrRelation : null

        const context: FilterContext = {
          isEntity: !!entity,
          isRelation: !!relation,
          type: entityOrRelation.type.id,
          data: entityOrRelation.data,
          isMainEntity: entity?.isMainEntity ?? false,
          isParent: element.data('isParent'),
          isChild: element.data('isChild'),
        }

        // Hide if at least one filter returns false
        if (filters.some(filter => !filter.filter(context))) {
          element.data('hiddenByFilter', true)

          // If the element is a node, hide connected edges
          if (element.isNode()) {
            const connectedEdges = element.connectedEdges() as cytoscape.CollectionReturnValue
            connectedEdges.data('hiddenByFilter', true)
          }
        }
      })

      // Find and hide visible nodes that have edges, but are dangling because of filters
      elements.nodes('[!hiddenByFilter]').not(`#${this.diagram.mainEntityId}`).forEach(node => {
        // Skip if no connected edges
        if (!node.connectedEdges().length) return

        // Skip if any of the connected edges are visible
        if (node.connectedEdges('[!hiddenByFilter]').length) return

        node.data('hiddenByFilter', true)
      })
    })
  }

  layout (layout?: LayoutDefinition | null, animate = layout?.animate ?? false) {
    const graph = this._graph
    if (!graph) return

    return new Promise<void>(resolve => {
      const elements = graph.elements('[!hiddenByFilter]')

      elements.layout({
        sort: (a, b) => {
          console.log('SORT', a, b)
          return 0
        },

        name: 'breadthfirst',
        fit: false,
        directed: layout?.directed ?? false,
        grid: layout?.grid ?? false,
        spacingFactor: layout?.spacingFactor ?? 1.3,
        maximal: layout?.maximal ?? false,
        avoidOverlap: true,
        animate: animate,
        animationDuration: animate ? layout?.animationDuration ?? 1000 : undefined,
        animationEasing: animate ? layout?.animationEasing ?? 'ease-out' as any : undefined,
        transform: layout?.inverted
          ? (_: any, pos: cytoscape.Position): cytoscape.Position => ({ x: -pos.x, y: -pos.y })
          : undefined,
        stop: () => {
          resolve()
        },
      }).run()
    })
  }

  fit () {
    const graph = this._graph
    if (!graph) return

    graph.fit(graph.elements('[!hiddenByFilter]'), this.fitPadding)
  }
}
