


































import { Vue, Component, Prop, Provide } from 'vue-property-decorator'
import TopBar from './TopBar.vue'
import Graph from './Graph.vue'
import Customizer from './Customizer.vue'
import Editor from './Editor.vue'
import GraphLegend from './GraphLegend.vue'
import GraphPrint from './GraphPrint.vue'
import Diagram, { Entity, Relation } from '@/diagram'
import { VueConstructor } from 'vue'
import PrintDialog from './PrintDialog.vue'

@Component({
  components: {
    TopBar,
    Graph,
    Customizer,
    Editor,
    GraphLegend,
    GraphPrint,
  },
})

export default class DiagramVue extends Vue {
  @Provide('diagramVm')
  get diagramVm () { return this }

  @Provide('diagram')
  @Prop({ type: Diagram, required: true })
  readonly diagram!: Diagram

  editorTarget: Entity | Relation | null = null

  dialogs: ({
    component: VueConstructor
    props?: any
  })[] = []

  mounted () {
    this.diagram.eventBus.on('editor.open', ({ target }) => this.editorTarget = target)
    this.diagram.eventBus.on('dataUpdated', () => this.editorTarget = null)
  }

  showDialog (component: VueConstructor, props?: any) {
    this.dialogs.push({ component, props })
  }

  onKeyDown (event: KeyboardEvent) {
    if (event.defaultPrevented) return

    const ctrlOrMeta = event.ctrlKey || event.metaKey

    if (ctrlOrMeta && event.key.toLowerCase() === 'p') {
      event.preventDefault()
      this.showDialog(PrintDialog, { mode: 'print' })
    }
  }
}
