

























































































import { Vue, Component, Prop, Model } from 'vue-property-decorator'
import FieldContainer from './FieldContainer.vue'
import TextField from './TextField.vue'
import RadioField from './RadioField.vue'
import ColorField from './ColorField.vue'
import { isRecord } from '@/util/type-guards'

interface EditorFieldOption {
  label: string
  value: any
}

const notSet = Symbol('Not set')

@Component({
  components: {
    FieldContainer,
    TextField,
    RadioField,
    ColorField,
  },
})

export default class EditorField extends Vue {
  @Prop(String)
  readonly id?: string

  @Prop({ type: String, required: true })
  readonly type!: string

  @Prop(String)
  readonly title?: string

  @Prop({ type: Boolean, default: true })
  readonly editable!: boolean

  @Prop(Array)
  readonly options?: unknown[]

  @Prop(String)
  readonly helperText?: string

  @Prop({ default: () => notSet })
  readonly initialValue?: any

  @Prop(String)
  readonly initialValueLabel?: string

  @Prop({ default: () => notSet })
  readonly resetValue?: any

  @Model('change')
  readonly value?: any

  finalType: 'text' | 'number' | 'radio-buttons' | 'color' = 'text'
  finalOptions: EditorFieldOption[] = []

  get hasInitialValue () {
    return this.initialValue !== notSet
  }

  get hasResetValue () {
    return this.resetValue !== notSet
  }

  get showChangedFrom () {
    return this.hasInitialValue && this.value !== this.initialValue
  }

  get showReset () {
    return this.editable && this.hasResetValue && this.value !== this.resetValue
  }

  mounted () {
    this.$watch(
      () => this.type,
      () => {
        this.finalType = 'text'

        switch (this.type) {
          case 'text':
          case 'number':
          case 'radio-buttons':
          case 'color': {
            this.finalType = this.type
            break
          }

          case 'boolean': {
            this.finalType = 'radio-buttons'
            this.finalOptions = [
              {
                label: 'Ja',
                value: true,
              },
              {
                label: 'Nej',
                value: false,
              },
            ]
            break
          }
        }
      },
      { immediate: true },
    )

    this.$watch(
      () => this.options,
      () => {
        if (this.type === 'boolean') return

        this.finalOptions = []

        for (const option of this.options ?? []) {
          if (isRecord(option)) {
            this.finalOptions.push({
              label: typeof option.label === 'string' ? option.label : '',
              value: option.value,
            })
          }
        }
      },
      { immediate: true },
    )
  }
}
