

























import { Vue, Component, Prop, Inject } from 'vue-property-decorator'
import Dialog from './Dialog.vue'
import ButtonGroup from './ButtonGroup.vue'
import Button from './Button.vue'
import { DiagramDataDefinition } from '@/types/DiagramData'
import DiagramVue from './Diagram.vue'

@Component({
  components: {
    Dialog,
    ButtonGroup,
    Button,
  },
})

export default class ConfirmAdditionDialog extends Vue {
  @Inject()
  readonly diagramVm!: DiagramVue

  @Prop({ type: Object, required: true })
  readonly data!: DiagramDataDefinition

  @Prop(Function)
  readonly onConfirm?: () => void

  @Prop(Function)
  readonly onDismiss?: () => void

  get diagram () {
    return this.diagramVm.diagram
  }

  get content () {
    const typeCounts = new Map<string, number>()

    for (const relation of this.data.relations ?? []) {
      const type = relation.type
      typeCounts.set(type, (typeCounts.get(type) ?? 0) + 1)
    }

    if (typeCounts.size) {
      let typeCountsString = ''

      let i = 0
      for (const [ typeId, count ] of typeCounts) {
        i++
        const type = this.diagram.relationTypes.get(typeId)

        if (typeCountsString) {
          typeCountsString += i == typeCounts.size
            ? ' og '
            : ', '
        }

        typeCountsString += `${count} ${type.labels[count == 1 ? 'singular' : 'plural'].toLowerCase()}`
      }

      return `Der vil blive tilfjet ${typeCountsString} til diagrammet. Vil du forsttte?`
    }

    return `Der vil blive tilfjet ${this.data.entities?.length ?? 0} entiteter til diagrammet. Vil du fortstte?`
  }

  close () {
    this.$emit('close')
  }

  confirm () {
    this.close()
    this.onConfirm?.()
  }

  dismiss () {
    this.close()
    this.onDismiss?.()
  }
}
