




























import { Vue, Component, Inject } from 'vue-property-decorator'
import DiagramVue from './Diagram.vue'

@Component({})

export default class GraphLegend extends Vue {
  @Inject()
  readonly diagramVm!: DiagramVue

  get diagram () {
    return this.diagramVm.diagram
  }

  get fields () {
    return this.diagram.fields.filter(field => field.active && field.addToLegend)
  }
}
