














import { Vue, Component, Prop, Model } from 'vue-property-decorator'
import EditorFields from './EditorFields.vue'
import EditorField from './EditorField.vue'
import { Field } from '@/diagram'
import _set from 'lodash.set'

@Component({
  components: {
    EditorFields,
    EditorField,
  },
})

export default class DataCreator extends Vue {
  @Prop({ type: Array, required: true })
  readonly fields!: Field[]

  @Model('change', { type: Object, default: () => ({}) })
  readonly data!: Record<string, any>

  get fieldStates () {
    return Vue.observable(this.fields.map(field => {
      const state = {
        field,
        value: field.getValue(this.data),
        onChange: (value: any) => {
          state.value = value
          if (field.dataKey) _set(this.data, field.dataKey, value)
          this.$emit('change', this.data)
        },
      }

      return state
    }))
  }
}
