

































import { PrintContext } from '@/diagram'
import html2canvas from 'html2canvas'
import { Vue, Component, Inject, Ref } from 'vue-property-decorator'
import DiagramVue from './Diagram.vue'
import Graph from './Graph.vue'
import GraphLegend from './GraphLegend.vue'

const nextFrame = () => new Promise<void>(resolve => requestAnimationFrame(() => resolve()))
const timeout = (ms = 0) => new Promise<void>(resolve => setTimeout(resolve, ms))

@Component({
  components: {
    Graph,
    GraphLegend,
  },
})

export default class GraphPrint extends Vue {
  @Inject()
  readonly diagramVm!: DiagramVue

  @Ref()
  readonly printContentEl?: HTMLDivElement

  printing = false
  scaling = 4
  title = ''

  mode: 'print' | 'pdf' | 'png' = 'print'
  orientation: 'portrait' | 'landscape' = 'landscape'
  size: 'A5' | 'A4' | 'A3' = 'A4'
  includeMargin = true

  get diagram () {
    return this.diagramVm.diagram
  }

  get finalSize () {
    const sizes = {
      'A5': [210, 148],
      'A4': [297, 210],
      'A3': [420, 297],
    }

    let size = sizes[this.size]

    if (this.orientation === 'portrait') size = [size[1], size[0]]

    return {
      width: size[0] * this.scaling,
      height: size[1] * this.scaling,
      margin: (this.includeMargin ? 10 : 0) * this.scaling,
    }
  }

  mounted () {
    this.diagram.eventBus.on('print', event => {
      if (this.printing) return
      this.printing = true
      this.mode = event.mode
      this.orientation = event.orientation
      this.size = event.size
      this.includeMargin = this.mode !== 'png' && event.includeMargin
    })
  }

  async onReadyToPrint () {
    const cleanup = () => {
      this.printing = false
      this.diagram.eventBus.emit({ name: 'printDone' })
    }

    if (!this.printing || !this.printContentEl) return cleanup()

    const { diagram } = this
    const { mainEntityId, activeDiagram } = diagram
    const { data } = activeDiagram

    const date = new Date()

    const context: PrintContext = {
      mainEntity: {
        id: mainEntityId,
        data: data.mainEntity?.data ?? {},
      },
      hasDataChanges: [
        ...data.initialData.changes,
        ...data.changes.map(c => c.change),
      ].some(change => change.add || change.remove || change.edit),
      date: date,
      formattedDate: [
        `${date.getDate()}`.padStart(2, '0'),
        `${date.getMonth() + 1}`.padStart(2, '0'),
        `${date.getFullYear()}`.substr(-2),
      ].join('.'),
      formattedTime: [
        `${date.getHours()}`.padStart(2, '0'),
        `${date.getMinutes()}`.padStart(2, '0'),
        `${date.getSeconds()}`.padStart(2, '0'),
      ].join(':'),
    }

    this.title = (
      this.diagram.methods.getPrintTitle?.(context)
      ?? `${context.hasDataChanges ? 'Manuelt tilpasset ejerstruktur' : 'Ejerstruktur'} d. ${context.formattedDate} kl. ${context.formattedTime}`
    )

    const filename = (
      (
        this.diagram.methods.getPrintFilename?.(context)
        ?? `ejerdiagram-${context.formattedDate}-${context.formattedTime}`
      )
      .replace(/[ .:]+/g, '-')
      .replace(/[^\w-]+/g, '')
    )

    // TODO : Make this less dumb
    await this.$nextTick()
    await timeout(500)

    const canvas = await html2canvas(this.printContentEl, {
      logging: false,
      scale: 1,
    })

    if (this.mode === 'print') {
      const printContentRect = this.printContentEl.getBoundingClientRect()

      const width = Math.floor(printContentRect.width)
      const height = Math.floor(printContentRect.height)

      const iFrameEl = document.createElement('iframe')
      iFrameEl.className = 'lassox-diagram__GraphPrint-iframe'
      iFrameEl.style.width = `${width}px`
      iFrameEl.style.height = `${height}px`
      document.body.appendChild(iFrameEl)

      const iFrameWindow = iFrameEl.contentWindow

      const cleanupPrint = () => {
        iFrameEl.remove()
        cleanup()
      }

      if (!iFrameWindow) return cleanup()

      iFrameWindow.document.head.innerHTML = [
        '<style>',
        '@page {',
        `  size: ${width}mm ${height}mm;`,
        `  margin: 0;`,
        '}',
        'html, body {',
        '  width: 100%;',
        '  height: 100%;',
        '  padding: 0;',
        '  margin: 0;',
        '  overflow: hidden;',
        '}',
        '</style>',
      ].join('\n')

      canvas.style.width = '100%'
      canvas.style.height = '100%'
      iFrameWindow.document.body.appendChild(canvas)

      iFrameWindow.addEventListener('afterprint', cleanupPrint)
      iFrameWindow.print()

      return
    }

    let blob = await new Promise<Blob | null>(resolve => canvas.toBlob(resolve, 'image/png'))

    if (!blob) {
      console.error('Something went wrong when trying to get canvas blob')
      return cleanup()
    }

    if (this.mode === 'pdf') {
      if (!this.diagram.methods.convertPngToPdf) return cleanup()

      blob = await this.diagram.methods.convertPngToPdf({
        blob,
        title: this.title,
        filename: `${filename}.png`,
        size: this.size,
        orientation: this.orientation,
      })
    }

    const url = window.URL.createObjectURL(blob)

    const aEl = document.createElement('a')
    aEl.href = url
    aEl.download = `${filename}.${this.mode === 'pdf' ? 'pdf' : 'png'}`
    document.body.appendChild(aEl)
    aEl.click()
    aEl.remove()

    window.URL.revokeObjectURL(url)

    cleanup()
  }
}
