
































import { Vue, Component, Inject } from 'vue-property-decorator'
import Dialog from './Dialog.vue'
import EntitySelector, { EntitySelectorModel } from './EntitySelector.vue'
import ButtonGroup from './ButtonGroup.vue'
import Button from './Button.vue'
import DiagramVue from './Diagram.vue'
import { EntityDefinition } from '@/diagram'
import _set from 'lodash.set'
import _cloneDeep from 'lodash.clonedeep'
import _merge from 'lodash.merge'

@Component({
  components: {
    Dialog,
    EntitySelector,
    ButtonGroup,
    Button,
  },
})

export default class AddEntityDialog extends Vue {
  @Inject()
  readonly diagramVm!: DiagramVue

  isConfirming = false

  entitySelectorModel: EntitySelectorModel = null

  get diagram () {
    return this.diagramVm.diagram
  }

  get canConfirm () {
    if (this.isConfirming) return false
    if (!this.entitySelectorModel) return false
    if (this.entitySelectorModel.mode === 'search' && this.entitySelectorModel.selectedSuggestion.entity) return false
    return true
  }

  close () {
    this.$emit('close')
  }

  async confirm () {
    const { canConfirm, entitySelectorModel } = this

    if (!canConfirm || !entitySelectorModel) return

    this.isConfirming = true

    const generateId = (prefix: string, items: Map<string, any>) => {
      const time = Date.now()
      let i = 0
      let id = ''
      while (!id || items.has(id)) {
        id = `${prefix}-${time}-${i}`
        i++
      }
      return id
    }

    let entityId: string
    let newEntity: EntityDefinition

    if (entitySelectorModel.mode === 'search') {
      const suggestion = entitySelectorModel.selectedSuggestion

      entityId = suggestion.id

      if (suggestion.entityDefinition) {
        newEntity = _cloneDeep(suggestion.entityDefinition)

        if (this.diagram.methods.getEntityData) {
          const entity = (await this.diagram.methods.getEntityData({
            ids: [ suggestion.id ],
            relationTypes: [],
            parents: 0,
            children: 0,
          })).entities?.find(e => e.id === suggestion.id)

          if (entity) newEntity = _merge(newEntity, entity)
        }
      } else {
        throw Error('AddEntityDialog cannot be used to add an entity that is already in the diagram')
      }
    } else {
      entityId = generateId('CUSTOM-ENTITY', this.diagram.activeDiagram.data.entitiesMap)

      newEntity = {
        type: entitySelectorModel.entityType.id,
        id: entityId,
        data: entitySelectorModel.entityData,
      }
    }

    this.diagram.activeDiagram.data.add({
      entities: [ newEntity ],
    })

    this.close()
  }
}
