import { DiagramConfig, SearchResultBuilder } from './diagram'
import { DiagramDataDefinition } from './types/DiagramData'

const mainEntityId = 'CVR-1-34580820'

const companyOrPersonSearchResultBuilder: SearchResultBuilder = context => {
  let address = typeof context.data.address1 === 'string' && context.data.address1 || ''

  const postalCode = typeof context.data.postalCode === 'number' && context.data.postalCode.toString() || ''
  const city = typeof context.data.city === 'string' && context.data.city || ''

  if (postalCode || city) {
    if (address) address += ','
    if (postalCode) address += ` ${postalCode}`
    if (city) address += ` ${city}`
  }

  return {
    text: context.data.name,
    subtext: address || null,
  }
}

