
































































































































































































































import { Vue, Component, Prop, Inject } from 'vue-property-decorator'
import ButtonGroup from './ButtonGroup.vue'
import Button from './Button.vue'
import IconButton from './IconButton.vue'
import DropdownMenu from './DropdownMenu.vue'
import DropdownMenuItem from './DropdownMenuItem.vue'
import DropdownMenuDivider from './DropdownMenuDivider.vue'
import Diagram from '@/diagram'
import DiagramVue from './Diagram.vue'
import ConfirmResetDialog from './ConfirmResetDialog.vue'
import PrintDialog from './PrintDialog.vue'
import SaveLoadDialog from './SaveLoadDialog.vue'
import AddEntityDialog from './AddEntityDialog.vue'
import ConfirmAdditionDialog from './ConfirmAdditionDialog.vue'

@Component({
  components: {
    ButtonGroup,
    Button,
    IconButton,
    DropdownMenu,
    DropdownMenuItem,
    DropdownMenuDivider,
  },
})

export default class TopBar extends Vue {
  @Inject()
  readonly diagramVm!: DiagramVue

  @Prop({ type: Diagram, required: true })
  readonly diagram!: Diagram

  showSaveAsDropdownMenu = false
  showLoadDropdownMenu = false
  showLayoutDropdownMenu = false
  showEditDropdownMenu = false

  get canUndo () {
    return this.diagram.activeDiagram.data.canUndo
  }

  get canRedo () {
    return this.diagram.activeDiagram.data.canRedo
  }

  showPrintDialog (mode: PrintDialog['mode']) {
    this.diagramVm.showDialog(PrintDialog, { mode })
  }

  save () {
    this.diagram.save(true)
  }

  saveAsNew () {
    this.diagram.save(false)
  }

  async expand () {
    const data = await this.diagram.methods.getEntityData?.({
      ids: this.diagram.activeDiagram.data.entities.map(e => e.id),
      relationTypes: this.diagram.relationTypes.items.map(rt => rt.id),
      parents: 1,
      children: 1,
    })

    if (!data) return

    if (data.entities) data.entities = data.entities.filter(entity => !this.diagram.activeDiagram.data.entitiesMap.has(entity.id))
    if (data.relations) data.relations = data.relations.filter(relation => !this.diagram.activeDiagram.data.relationsMap.has(relation.id))
    if (!data.entities?.length && !data.relations?.length) return

    this.diagramVm.showDialog(ConfirmAdditionDialog, {
      data,
      onConfirm: () => this.diagram.activeDiagram.data.add(data),
    })
  }

  undo () {
    this.diagram.activeDiagram.data.undo()
  }

  redo () {
    this.diagram.activeDiagram.data.redo()
  }

  reset () {
    this.diagramVm.showDialog(ConfirmResetDialog, {
      onConfirm: () => this.diagram.reset(),
    })
  }

  showSaveLoadDialog () {
    this.diagramVm.showDialog(SaveLoadDialog)
  }

  showAddDialog () {
    this.diagramVm.showDialog(AddEntityDialog)
  }
}
