export function parsePath (path: string | string[]) {
  if (typeof path == 'string') {
    path = path
      // Convert indexes to properties
      .replace(/\[(\w+)\]/g, '.$1')
      // Split by .
      .split('.')
  }

  if (!path.length) throw Error(`Invalid path: ${path.join('.')}`)

  return path
}

export function getDeep (obj: any, path: string | string[]) {
  path = parsePath(path)

  let curr = obj

  for (const prop of path) {
    if (prop in curr) curr = curr[prop]
    else return undefined
  }

  return curr
}

export function setDeep (obj: any, path: string | string[], value: any) {
  path = parsePath(path)

  const targetParent = path.length == 1
    ? obj
    : getDeep(obj, path.slice(0, -1))

  if (!targetParent) return

  const targetProp = path[path.length - 1]

  targetParent[targetProp] = value
}
