import Diagram, { EntityDefinition, EntityStyle, RelationDefinition, RelationStyle } from '@/diagram'
import { Change, DiagramDataDefinition, EntityEdit, RelationEdit } from './DiagramData'

export default interface SavedDiagram {
  id: string
  title?: string
  description?: string
  shared?: boolean
  mainEntityId: string
  data?: DiagramDataDefinition
  changes?: Change[]
  settings?: {
    activeFields?: string[]
    activeFilters?: string[]
    activeLayout?: string
  }
}

export const createSavedDiagram = (diagram: Diagram): SavedDiagram => {
  return {
    id: diagram.activeDiagram.id,
    title: diagram.activeDiagram.title,
    description: diagram.activeDiagram.description,
    shared: diagram.activeDiagram.shared,
    mainEntityId: diagram.mainEntityId,
    data: {
      entities: diagram.activeDiagram.data.initialData.entities.map(e => ({
        type: e.type.id,
        id: e.id,
        ...e.initialState,
      })),
      relations: diagram.activeDiagram.data.initialData.relations.map(r => ({
        type: r.type.id,
        id: r.id,
        from: r.from,
        to: r.to,
        ...r.initialState,
      })),
    },
    changes: [
      ...diagram.activeDiagram.data.initialData.changes,
      ...diagram.activeDiagram.data.changes.map(c => c.change),
    ],
    settings: {
      activeFields: Object.keys(diagram.activeDiagram.settings.activeFields).filter(id => diagram.activeDiagram.settings.activeFields[id]),
      activeFilters: Object.keys(diagram.activeDiagram.settings.activeFilters).filter(id => diagram.activeDiagram.settings.activeFilters[id]),
      activeLayout: diagram.activeDiagram.settings.activeLayout?.id,
    },
  }
}

export const parseSavedDiagramJSON = (json: string): SavedDiagram | null => {
  const obj: unknown = JSON.parse(json)

  if (!isRecord(obj)) return null

  if (typeof obj.id !== 'string') return null
  const id = obj.id

  if (!isNullish(obj.title) && typeof obj.title !== 'string') return null
  const title = obj.title ?? undefined

  if (!isNullish(obj.description) && typeof obj.description !== 'string') return null
  const description = obj.description ?? undefined

  if (!isNullish(obj.shared) && typeof obj.shared !== 'boolean') return null
  const shared = obj.shared ?? undefined

  if (typeof obj.mainEntityId !== 'string') return null
  const mainEntityId = obj.mainEntityId

  const data = parseData(obj.data)
  const changes = parseChanges(obj.changes)
  const settings = parseSettings(obj.settings)

  return {
    id,
    title,
    description,
    shared,
    mainEntityId,
    data,
    changes,
    settings,
  }
}

const parseData = (v: unknown): SavedDiagram['data'] => {
  if (!isRecord(v)) return
  return {
    entities: parseEntities(v.entities),
    relations: parseRelations(v.relations),
  }
}

const parseChanges = (v: unknown): SavedDiagram['changes'] => {
  if (!isArray(v)) return

  const changes: SavedDiagram['changes'] = []

  for (const change of v) {
    if (!isRecord(change)) continue

    const parsedChange: Change = {}

    if (isRecord(change.add)) {
      parsedChange.add = {
        entities: parseEntities(change.add.entities),
        relations: parseRelations(change.add.relations),
      }
    }

    if (isRecord(change.remove)) {
      parsedChange.remove = {
        entities: parseStringArray(change.remove.entities),
        relations: parseStringArray(change.remove.relations),
      }
    }

    if (isRecord(change.edit)) {
      parsedChange.edit = {
        entities: (() => {
          if (!isArray(change.edit.entities)) return

          const entities: EntityEdit[] = []
          for (const entity of change.edit.entities) {
            if (!isRecord(entity) || !isString(entity.id)) continue

            entities.push({
              id: entity.id,
              data: parseRecord(entity.data),
              style: parseEntityStyle(entity.style),
            })
          }

          return entities
        })(),
        relations: (() => {
          if (!isArray(change.edit.relations)) return

          const relations: RelationEdit[] = []
          for (const relation of change.edit.relations) {
            if (!isRecord(relation) || !isString(relation.id)) continue

            relations.push({
              id: relation.id,
              data: parseRecord(relation.data),
              style: parseRelationStyle(relation.style),
            })
          }

          return relations
        })(),
      }
    }

    if (isString(change.layout)) {
      parsedChange.layout = change.layout
    }

    if (isArray(change.moveEntities)) {
      parsedChange.moveEntities = []

      for (const moveEntity of change.moveEntities) {
        if (!isRecord(moveEntity) || !isString(moveEntity.id) || !isNumber(moveEntity.x) || !isNumber(moveEntity.y)) continue
        parsedChange.moveEntities.push({
          id: moveEntity.id,
          x: moveEntity.x,
          y: moveEntity.y,
        })
      }
    }

    changes.push(parsedChange)
  }

  return changes
}

const parseEntities = (v: unknown): EntityDefinition[] | undefined => {
  if (!isArray(v)) return
  const entities: EntityDefinition[] = []
  for (const e of v) {
    const entity = parseEntity(e)
    if (entity) entities.push(entity)
  }
  return entities
}

const parseRelations = (v: unknown): RelationDefinition[] | undefined => {
  if (!isArray(v)) return
  const relations: RelationDefinition[] = []
  for (const r of v) {
    const relation = parseRelation(r)
    if (relation) relations.push(relation)
  }
  return relations
}

const parseEntity = (v: unknown): EntityDefinition | null => {
  if (!isRecord(v) || !isString(v.type) || !isString(v.id)) return null

  return {
    type: v.type,
    id: v.id,
    position: (() => {
      if (!isRecord(v.position) || !isNumber(v.position.x) || !isNumber(v.position.y)) return
      return { x: v.position.x, y: v.position.y }
    })(),
    data: parseRecord(v.data),
    style: parseEntityStyle(v.style),
  }
}

const parseEntityStyle = (v: unknown): EntityStyle | undefined => {
  if (!isRecord(v)) return

  const style: EntityStyle = {}
  if (isString(v.backgroundColor)) style.backgroundColor = v.backgroundColor
  if (isString(v.borderColor)) style.borderColor = v.borderColor
  if (isEnum(v.borderStyle, ['solid', 'dotted', 'dashed'])) style.borderStyle = v.borderStyle
  if (isNumber(v.borderWidth)) style.borderWidth = v.borderWidth
  if (isString(v.labelsBackgroundColor)) style.labelsBackgroundColor = v.labelsBackgroundColor
  return style
}

const parseRelation = (v: unknown): RelationDefinition | null => {
  if (!isRecord(v)) return null

  if (!isString(v.type)) return null
  const type = v.type

  if (!isString(v.id)) return null
  const id = v.id

  if (!isString(v.from)) return null
  const from = v.from

  if (!isString(v.to)) return null
  const to = v.to

  return {
    type,
    id,
    from,
    to,
    data: parseRecord(v.data),
    style: parseRelationStyle(v.style),
  }
}

const parseRelationStyle = (v: unknown): RelationStyle | undefined => {
  if (!isRecord(v)) return

  const style: RelationStyle = {}
  if (isBoolean(v.arrowFrom)) style.arrowFrom = v.arrowFrom
  if (isBoolean(v.arrowTo)) style.arrowTo = v.arrowTo
  if (isNumber(v.arrowSize)) style.arrowSize = v.arrowSize
  if (isNumber(v.lineWidth)) style.lineWidth = v.lineWidth
  if (isEnum(v.lineStyle, ['solid', 'dotted', 'dashed'])) style.lineStyle = v.lineStyle
  if (isString(v.lineColor)) style.lineColor = v.lineColor
  return style
}

const parseRecord = (v: unknown): Record<string, unknown> | undefined => {
  if (!isRecord(v)) return
  return v
}

const parseSettings = (v: unknown): SavedDiagram['settings'] => {
  if (!isRecord(v)) return

  const settings: SavedDiagram['settings'] = {}

  if (isArray(v.activeFields)) {
    settings.activeFields = []
    for (const id of v.activeFields) {
      if (isString(id)) settings.activeFields.push(id)
    }
  }

  if (isArray(v.activeFilters)) {
    settings.activeFilters = []
    for (const id of v.activeFilters) {
      if (isString(id)) settings.activeFilters.push(id)
    }
  }

  if (isString(v.activeLayout)) {
    settings.activeLayout = v.activeLayout
  }

  return settings
}

const parseStringArray = (v: unknown): string[] | undefined => {
  if (!isArray(v)) return
  const array: string[] = []
  for (const item of v) {
    if (isString(item)) array.push(item)
  }
  return array
}

const isNullish = (v: any): v is null | undefined => v === null || v === undefined

const isBoolean = (v: any): v is boolean => typeof v === 'boolean'

const isString = (v: any): v is string => typeof v === 'string'

const isNumber = (v: any): v is number => typeof v === 'number'

const isEnum = <T extends string> (v: any, values: T[]): v is T => values.includes(v)

const isRecord = <K extends string = string> (v: any): v is Record<K, unknown> => typeof v == 'object' && !isArray(v) && v !== null

const isArray = (v: any): v is unknown[] => Array.isArray(v)
