




































































import { Vue, Component, Inject, Prop } from 'vue-property-decorator'
import Dialog from './Dialog.vue'
import EditorFields from './EditorFields.vue'
import EditorField from './EditorField.vue'
import ButtonGroup from './ButtonGroup.vue'
import Button from './Button.vue'
import DiagramVue from './Diagram.vue'
import { isRecord } from '@/util/type-guards'

@Component({
  components: {
    Dialog,
    EditorFields,
    EditorField,
    ButtonGroup,
    Button,
  },
})

export default class PrintDialog extends Vue {
  @Inject()
  readonly diagramVm!: DiagramVue

  @Prop({ type: String, required: true })
  readonly mode!: 'print' | 'pdf' | 'png'

  orientation: 'portrait' | 'landscape' = 'landscape'
  size: 'A5' | 'A4' | 'A3' = 'A4'
  includeMargin = true

  printing = false

  get diagram () {
    return this.diagramVm.diagram
  }

  get title () {
    switch (this.mode) {
      case 'print': return 'Udskriv'
      case 'pdf': return 'Gem som PDF'
      case 'png': return 'Gem som billede'
      default: return 'Udskriv'
    }
  }

  get description () {
    switch (this.mode) {
      case 'print':
        return 'Din computers udskriftsindstillinger skal vre de samme som de nedenfor valgte.'
      case 'pdf':
        return 'Gem diagrammet til din computer som PDF.'
      case 'png':
        return 'Gem diagrammet til din computer i .png format.'
      default:
        return ''
    }
  }

  created () {
    this.loadFromLocalStorage()

    this.$watch(
      () => [this.orientation, this.size, this.includeMargin],
      this.saveToLocalStorage,
      { immediate: true },
    )
  }

  loadFromLocalStorage () {
    if (!this.diagram.saveSettingsToLocalStorage) return

    try {
      const json = localStorage.getItem('lassox-diagram/print-settings')
      if (!json) return

      const settings = JSON.parse(json)
      if (!isRecord(settings)) return

      const { orientation, size, includeMargin } = settings

      if (orientation === 'portrait' || orientation === 'landscape') {
        this.orientation = orientation
      }

      if (size === 'A5' || size === 'A4' || size === 'A3') {
        this.size = size
      }

      if (typeof includeMargin === 'boolean') {
        this.includeMargin = includeMargin
      }
    } catch {
      return
    }
  }

  saveToLocalStorage () {
    if (!this.diagram.saveSettingsToLocalStorage) return

    try {
      localStorage.setItem('lassox-diagram/print-settings', JSON.stringify({
        orientation: this.orientation,
        size: this.size,
        includeMargin: this.includeMargin,
      }))
    } catch {
      return
    }
  }

  close () {
    this.$emit('close')
  }

  confirm () {
    this.printing = true

    this.$nextTick(() => {
      this.diagram.eventBus.one('printDone', () => {
        this.printing = false
        this.close()
      })

      this.diagram.eventBus.emit({
        name: 'print',
        mode: this.mode,
        orientation: this.orientation,
        size: this.size,
        includeMargin: this.includeMargin,
      })
    })
  }
}
