import { Callback, unwatchRect, watchRect } from './watch-rect'

const el = document.createElement('div')

el.style.position = 'fixed'
el.style.top = '0'
el.style.left = '0'
el.style.right = '0'
el.style.bottom = '0'
el.style.zIndex = '-9999'
el.style.visibility = 'hidden'

document.body.appendChild(el)

export function watchViewport (callback: Callback) {
  return watchRect(el, callback)
}

export function unwatchViewport (callback: Callback) {
  return unwatchRect(el, callback)
}
